@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*breadcrumbs*/
$bread-item-max-width: 320px;
$bread-item-min-width: 200px;
$bread-space-horizontal: 20px;
$bread-space-vertical: 10px;

.breadcrumbs {

	.home-page & {
		display: none; }

	& {
		position: relative;
		@include font($font-size-small, 21px);
		color: $text-color;
		margin-bottom: $bottom-space;
		cursor: default;
		z-index: 35;
		@include clear;

		@include media('<desk-hd') {
			margin-bottom: 0.5rem; } } }

.breadcrumbs__list {
	@include clear; }

.breadcrumbs__item {
	float: left;
	position: relative;
	margin: 0 10px 1rem 0;
	padding: 0;

	@include media('<desk-hd') {
		margin-bottom: 0.5rem; }

	&:first-child {
		margin-left: 0; }

	& > span {
		display: inline-block;
		vertical-align: top;
		@include truncate($bread-item-max-width);

		@include media('<mob-lg') {
			max-width: 240px; } }

	& > a {
		display: inline-block;
		vertical-align: top;
		position: relative;
		padding: 0 18px 0 0;
		color: $text-color-alt;
		text-decoration: none;
		min-width: 0;
		cursor: pointer;
		transition-property: max-width, opacity, visibility, border-color;
		transition-duration: $animation-speed;

		& > span {
			display: block;
			position: relative;
			@include truncate($bread-item-max-width);
			border-bottom: 1px solid transparent;
			z-index: 5;
			transition-property: border-color;
			transition-duration: $animation-speed;

			@include media('<mob-lg') {
				max-width: 240px; } }

		.no-touchevents &:hover > span {
			border-bottom-color: tint($text-color-alt, 40%); }

		& > i {
			position: absolute;
			right: 0;
			top: 7px;
			@include size(7px, 8px);
			pointer-events: none;

			&::before {
				content: '';
				position: absolute;
				left: 0;
				top: 50%;
				height: 0;
				width: 0;
				border: solid transparent;
				border-width: 4px 0 4px 7px;
				margin-top: -4px;
				border-left-color: $mark-color; } } }

	& > i {
		display: none; }

	&_has-drop:last-of-type, &-drop:last-child, &_last {
		margin-right: 0;

		& > a {
			padding-right: 0;

			& > i {
				display: none; } }

		& > i {
			display: none; } }

	&:last-of-type, &:last-child, &_last, &.hover, &_long {

		& > a {
			max-width: $bread-item-max-width; } }

	&_short {
		> a {
			max-width: none; } } }

.breadcrumbs-drop {
	position: absolute;
	top: -#{$bread-space-vertical};
	left: -#{$bread-space-horizontal};
	padding: 0 $bread-space-horizontal;
	opacity: 0;
	visibility: hidden;
	white-space: nowrap;
	background: #fff;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
	z-index: 10;
	transition-property: max-width, opacity, visibility, border-color;
	transition-duration: $animation-speed;

	.breadcrumbs__item_has-drop.hover > & {
		opacity: 1;
		visibility: visible;
		transition-delay: 0.1s; } }

.breadcrumbs-drop__holder {
	position: relative; }

.breadcrumbs-drop__item {
	margin: #{$bread-space-vertical} 0;

	a {
		display: block;
		text-decoration: none;
		color: $text-color;
		cursor: pointer;
		position: relative;

		& > span {
			display: inline-block;
			vertical-align: top;
			border-bottom: 1px solid transparent;
			transition-property: max-width, opacity, visibility, border-color;
			transition-duration: $animation-speed;
			@include truncate($bread-item-max-width); }

		.no-touchevents &:hover > span {
			border-bottom-color: $text-color-light; } } }

/*breadcrumbs end*/
