@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*article meta*/
@mixin article-meta-text-size() {
	@include font(1.4rem, round-float(18/14, 2)); }

.article-meta {
	color: $text-color-light;
	margin-bottom: $bottom-space-md;
	@include clear;

	@include media('<desk-hd') {
		margin-bottom: 1rem; }

	@include media('<mob-lg') {
		margin-bottom: 0.5rem; } }

/*article meta end*/

/*date*/
.datetime {
	float: left;
	display: block;
	margin-bottom: $bottom-space;
	@include font-bold;
	@include article-meta-text-size;
	color: $base-color;
	font-style: normal;

	@include media('<desk-hd') {
		margin-bottom: 1rem; }

	@include media('<mob-lg') {
		margin-bottom: 0.5rem; }

	.article-meta & {
		margin-right: 20px;
		margin-bottom: 0; } }

/*date end*/

/*visit*/
.visit-view {
	svg {
		@include display-inline-block-middle;
		@include size(18px);
		margin-right: 10px;

		path {
			fill: currentColor;
			transition-property: fill;
			transition-duration: $animation-speed; }

		&.depict-visit-share {
			@include size(12px); } }

	& > em {
		@include display-inline-block-middle;
		font-style: normal; } }

.visit {
	overflow: hidden;
	margin-left: 20px;
	text-align: right;
	@include article-meta-text-size;
	color: $text-color-light;
	//cursor: default
	display: none; }

.visit__item {
	display: inline-block;
	vertical-align: top;
	margin-left: 30px;

	@include media('<mob-lg') {
		margin-left: 5px; } }

.visit__inner {
	display: inline-block;
	text-decoration: none;
	color: $text-color-light; }

/*visit end*/

/*ctrl*/
.ctrl {
	.article-layout &:last-child {
		margin-bottom: -#{$article-layout-space-vertical};

		@include media('<desk-hd') {
			margin-bottom: -#{$article-layout-space-vertical--desk-hd}; }

		@include media('<tab-lg') {
			margin-bottom: -#{$article-layout-space-vertical--tab-lg}; }

		@include media('<mob-lg') {
			margin-bottom: -#{$article-layout-space-vertical--mob-lg}; } } }

.ctrl__holder {
	@include clear; }

.ctrl__buttons {
	float: left;
	@include clear;

	@include media('<desk-hd') {
		float: none;
		margin-bottom: $bottom-space; }

	& > a {
		float: left;
		margin-right: $grid-layout-space--desk-hd;
		margin-bottom: 0;

		&:nth-child(3) {
			clear: left;

			&, & ~ a {
				margin-top: 20px; } }

		&.view-print {
			@include media('<desk-hd') {
				float: right;
				@include margin-horizontal($grid-layout-space--desk-hd, 0); }

			@include media('<tab-lg') {
				margin: 0; }

			@include media('<mob-md') {
				float: none; } }

		@include media('<tab-lg') {
			margin: 0; }

		@include media('<mob-md') {
			float: none; } }

	& > [class*="btn-"] {
		max-width: 270px;

		@include media('<mob-md') {
			display: block;
			max-width: none;
			margin-bottom: $bottom-space;

			&:last-child {
				margin-bottom: 0; } } } }

.ctrl .share-box {
	float: right;

	.share-box__label {
		@include font(1.6rem, 1.25);
		display: inline-block;
		vertical-align: top;
		max-width: 100px; }

	.social-networks {
		display: inline-block;
		vertical-align: top;

		li {
			@include margin(null, 0, null, 10px); } }

	@include media('<mob-lg') {
		float: none;

		.share-box__label {
			float: none;
			margin-bottom: $bottom-space-sm;
			max-width: none; }

		.social-networks {
			display: block; } } }

/*ctrl end*/
