@import "../utils/variables";
@import "../utils/mixins";

/*FOOTER*/
.footer {
	position: relative;
	clear: both;
	z-index: 100;
	color: #b2b2b2;

	&.isBottoming {
		height: 0; }

	.layout {
		position: relative; } }

.footer__holder {
	overflow: hidden;
	cursor: default;

	.isBottoming & {
		position: absolute;
		top: auto;
		right: auto;
		left: $sidebar-left-width;
		width: calc(100% - #{$sidebar-left-width});
		bottom: 0;

		@include media('<desk-sm') {
			left: 0;
			width: 100%; } } }

/*footer gov*/
.footer-gov {
	padding: $grid-layout-space;
	position: relative;
	overflow: hidden;
	background: white;

	@include media('<desk-hd') {
		padding: $grid-layout-space--desk-hd; }

	@include media('<desk-md') {
		padding: $grid-layout-space--desk-md; }

	@include media('<mob-lg') {
		padding: $grid-layout-space--mob-lg; }

	&::before {
		@include pseudo();
		top: -5%;
		left: -40px;
		width: 40px;
		height: 110%;
		box-shadow: 0 10px 15px rgba(0, 0, 0, 0.05);

		@include media('<desk-sm') {
			content: none;
			display: none; } } }

.gov {
	position: relative; }

.gov__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end
	margin: 0 -#{round($grid-space/2)};

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 }		// after slide load end

	.slick-dots {
		bottom: 0;
		top: 100%;
		margin-top: 20px;

		li {

			button {
				@include size(10px);
				border-radius: 0;
				background: #ccc;

				&::before {
					left: 0;
					top: 0;
					@include size(100%);
					border-radius: 0;
					box-shadow: none;
					background: none; } }

			&.slick-active button::before {
				opacity: 1;
				background: $text-color-alt; } } } }

.gov__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end
	padding: $grid-space/2;
	@include font($font-size-small, $line-height-small);
	letter-spacing: normal;
	color: $text-color;
	text-decoration: none;
	transition-property: background-color;
	transition-duration: $animation-speed;

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 }		// after slide load end

	.no-touchevents &:hover {
		background-color: $text-color-ultralight; }

	img {
		margin-bottom: $bottom-space/2; }

	p {
		margin-bottom: $bottom-space/2; }

	& > *:last-child {
		margin-bottom: 0; } }

.gov__link {
	color: $text-color-alt;
	border-bottom: 1px solid tint($text-color-alt, 40%);
	transition-property: border-color;
	transition-duration: $animation-speed;

	.no-touchevents &:hover {
		border-bottom-color: transparent; } }

/*footer gov end*/

/*footer common*/
.footer-common {
	padding: $grid-layout-space;
	position: relative;
	overflow: hidden;
	background: #001e33;

	@include media('<desk-hd') {
		padding: $grid-layout-space--desk-hd; }

	@include media('<desk-md') {
		padding: $grid-layout-space--desk-md; }

	@include media('<mob-lg') {
		padding: $grid-layout-space--mob-lg; }

	$bottom-space: 24px;
 }	// footer nav

.footer-nav {
	margin-bottom: 0;

	p {
		margin-bottom: $bottom-space; } }

.footer-nav__col {
	position: relative;
	@include font(1.4rem, 1.4);
	color: #fff;

	@include media('<mob-lg') {
		margin-bottom: 4rem; }

	& > *:last-child {
		margin-bottom: 0; } }

.footer-nav__title {
	display: block;
	font: 400 12px/1.4 $font-alt;
	text-transform: uppercase;
	color: #999;
	margin-bottom: $bottom-space;

	@include media('<mob-lg') {
		margin-bottom: $bottom-space-sm; }

	a {
		color: inherit;
		text-decoration: none;

		& > span {
			border-bottom: 1px solid transparent;
			transition-property: border-color;
			transition-duration: $animation-speed; }

		.no-touchevents &:hover > span {
			border-bottom-color: rgba(255, 255, 255, 0.30); } } }

.footer-nav__links {
	margin: 0 0 30px;

	li {
		display: inline-block;
		vertical-align: top;
		margin: 0 30px 10px 0;

		@include media('<tab-lg') {
			display: block;
			margin-right: 0; } }

	a {
		color: inherit;
		font-size: inherit;
		line-height: inherit;
		text-decoration: none;
		list-style: none;

		& > span {
			border-bottom: 1px solid transparent;
			transition-property: border-color;
			transition-duration: $animation-speed; }

		.no-touchevents &:hover > span {
			border-bottom-color: rgba(255, 255, 255, 0.30); } } }

/*footer common end*/

/*footer bottom*/
.footer-bottom {
	padding: 0 $grid-layout-space;
	background: #001e33;
	color: #b2b2b2;
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	@include font($font-size-small, $line-height-small);

	@include media('<desk-hd') {
		@include padding-horizontal($grid-layout-space--desk-hd); }

	@include media('<desk-md') {
		@include padding-horizontal($grid-layout-space--desk-md); } }

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;
	padding-top: 19px;
	padding-bottom: 19px;
	//box-sizing: border-box

	font-size: 1px;
	line-height: 0;
	letter-spacing: -4px;

	&::after {
		content: "1";
		margin-left: 100%;
		height: 1px;
		overflow: hidden;
		display: inline-block; } }

.footer-bottom__col {
	display: inline-block;
	vertical-align: middle;
	text-align: left;
	@include font($font-size-small, $line-height-small);
	padding: 0;
	letter-spacing: normal;
	max-width: calc(32% - 10px);

	@include media('<mob-lg') {
		display: block;
		text-align: left;
		max-width: none;
		padding-bottom: $bottom-space-sm; }

	&:nth-child(n+2) {
		text-align: center;

		@include media('<mob-lg') {
			text-align: left; } }

	&:last-child {
		text-align: right;

		@include media('<mob-lg') {
			text-align: left;
			padding-bottom: 0; } }

	a {
		color: #b2b2b2;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } }

	.copyright {
		display: inline-block;
		vertical-align: middle; } }

/*footer bottom end*/

/*developer*/
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__logo {
	display: inline-block;
	vertical-align: top;
	position: relative;
	top: 3px;
	width: 154px;
	height: 20px;
	text-align: left;
	text-indent: -9999px;
	padding-right: 3px;

	&::before, &::after {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 149px;
		height: 16px; }

	&::before {
		content: '';
		position: absolute;
		left: 4px;
		top: 4px;
		background-color: transparent;
		background-image: linear-gradient(45deg, black 25%, transparent 25%, transparent 75%, black 75%, black), linear-gradient(45deg, black 25%, transparent 25%, transparent 75%, black 75%, black);
		background-size: 2px 2px;
		background-position: 0 0, 1px 1px;

		.no-cssgradient &,
		.no-multiplebgs & {
			background: #000;
			opacity: 0.5;
			-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=50);
			filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=50); } }

	&::after {
		content: '';
		position: absolute;
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px; } }

.no-touchevents a:hover .developer__logo {
	&::before {
		transform: translate(1px, 2px); }

	&::after {
		transform: translate(0, -2px); } }

/*developer end*/

/*orphus */
.orphus {
	display: inline-block;
	vertical-align: top;
	background-color: rgba(243, 243, 243, 0.60);
	padding: 8px;
	border-radius: 2px;
	color: $text-color;

	b {
		display: inline-block;
		vertical-align: top;
		@include font-bold;
		text-transform: uppercase; } }

/*orphus end */
/*FOOTER end*/
