@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*user form*/
.form-title {
	display: block;
	@include font-title;
	padding-bottom: $bottom-space;
	color: $text-color-alt;
	font-size: 2.4rem;
	line-height: 1.2;
	text-align: center; }

.user-form {
	background-color: #fafafa;
	padding: $article-layout-space-vertical $article-layout-space-horizontal;
	border: 1px solid $border-color;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	@include media('<desk-hd') {
		padding: $article-layout-space-vertical--desk-hd $article-layout-space-horizontal--desk-hd; }

	@include media('<tab-lg') {
		padding: $article-layout-space-vertical--tab-lg $article-layout-space-horizontal--tab-lg; }

	@include media('<mob-lg') {
		padding: $article-layout-space-vertical--mob-lg $article-layout-space-horizontal--mob-lg;
		@include margin-horizontal(-#{$article-layout-space-horizontal--mob-lg});
		border-width: 1px 0; }

	form {
		display: block; }

	textarea {
		height: 152px;
		max-height: 152px;
		resize: none; }

	.captcha {
		@include clear;

		img {
			$margin-right: 20px;
			display: inline-block;
			vertical-align: middle;
			max-width: calc(50%);
			margin-right: $margin-right;

			& + input {
				display: inline-block;
				vertical-align: middle;
				width: auto;
				max-width: calc(50% - #{$margin-right} - 10px); } }

		@include media('<tab-lg') {
			img {
				display: block;
				max-width: none;
				margin-bottom: $bottom-space-sm;

				& + input {
					display: block;
					max-width: none;
					width: 100%; } } } }

	.form-footer {
		@include clear;

		@include media('<mob-lg') {
			padding-top: $bottom-space-sm; } }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		.btn-submit {
			width: 200px; } }

	.form-note {
		overflow: hidden;
		min-height: 50px;
		font-size: 0;
		letter-spacing: -4px;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size-small, $line-height-small);
		letter-spacing: normal; } }

/*user form end*/
