//.fotorama:not(.fotorama--fullscreen)
.fotorama {
	.fotorama__arr {
		top: 0;
		@include size(50px, 100%);
		display: block;
		margin-top: 0;
		position: absolute;
		background: none;
		outline: none;
		color: white;

		@include media('<mob-lg') {
			width: 40px; }

		&:hover {
			opacity: 1; }

		&.fotorama__arr--disabled {
			opacity: 0.25;
			cursor: default; }

		&::before {
			@include pseudo();
			@include size(50px);
			top: 50%;
			transform: translate(0, -50%);
			background-color: $base-color;

			@include media('<mob-lg') {
				@include size(40px);
				background-color: rgba($base-color, 0.80); } }

		&::after {
			@include pseudo();
			top: 50%;
			opacity: 1;
			pointer-events: none;
			@include size(12px);
			transform: rotate(45deg);
			border: 0 solid white; } }

	.fotorama__arr--prev {
		left: 0;

		&::after {
			left: 50%;
			border-width: 0 0 2px 2px;
			margin: -6px 0 0 -4px; } }

	.fotorama__arr--next {
		right: 0;

		&::after {
			right: 50%;
			border-width: 2px 2px 0 0;
			margin: -6px -4px 0 0; } } }

.fotorama__fullscreen-icon {
	@include size(auto, 50px);
	top: 0;
	right: 0;
	background-image: url(../../img/zoom.svg);
	background-color: rgba(0, 0, 0, 0.36);
	background-position: 20px 50%;
	background-size: 20px;
	padding: 15px 20px 15px 50px;
	@include font-alt-norm;
	@include font(16px, 20px);
	color: white;
	z-index: 20;

	@include media('<desk-lg') {
		@include font(0, 0);
		padding: 0;
		@include size(50px);
		background-position: 50% 50%; }

	@include media('<mob-lg') {
		@include size(40px); }

	&::before {
		content: "Увеличить фото";

		html[lang*="be"] & {
			content: "Павялічыць фота"; }

		html[lang*="en"] & {
			content: "Enlarge picture"; } }

	.fotorama--fullscreen & {
		background-image: url(../../img/close.svg);
		background-position: 50% 50%;
		background-size: 20px;
		@include size(50px);
		padding: 0;

		@include media('<mob-lg') {
			@include size(40px); }

		&::before, &::after {
			content: none; } } }

// caption
.fotorama__caption {
	display: none;

	.fotorama--fullscreen & {
		display: block; } }

.fotorama__caption__wrap {
	background-color: rgba(0, 0, 0, 0.5);
	color: white;
	margin: 0 auto;
	max-width: calc(100% - 120px);

	.fotorama--fullscreen & {
		max-width: none; } }

