/*nav item arrow */
.nav__angle {
	position: absolute;
	right: 30px;
	top: 50%;
	width: 7px;
	height: 13px;
	margin-top: -6px;
	display: none;
	cursor: default;
	pointer-events: none;
}

.nav__angle::before, .nav__angle::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: 50%;
	width: 8px;
	height: 2px;
	border-radius: 4px;
	background: #262626;
	transition-property: background;
	transition-duration: 0.2s;
}

.nav__angle::before {
	transform: rotate(45deg);
	margin: -3px 0 0 -1px;
}

.nav__angle::after {
	transform: rotate(-45deg);
	margin: 1px 0 0 -1px;
}

/*nav item arrow end */
/*nav drop common */
.nav__drop {
	display: block;
	position: absolute;
	left: 0;
	top: 100%;
	width: 320px;
	background-color: #f2f2f2;
	border: 1px solid #d9d9d9;
	box-shadow: 0 8px 25px rgba(0, 0, 0, 0.25);
	z-index: 100;
	opacity: 0;
	visibility: hidden;
	transition-property: opacity, visibility, transform;
	transition-duration: 0.2s;
	transition-timing-function: cubic-bezier(0.25, 1, 0.25, 1);
