	margin-left: -60px;
	left: 100%;
	width: 60px;
	z-index: 50;
}

@media (max-width: 1919px) {
	.side {
		margin-top: 25px;
	}
}

@media (max-width: 1279px) {
	.side {
		display: none;
	}
}

