	}
}

.side__list {
	width: 60px;
	position: relative;
	z-index: 10;
}

.side__item {
	position: relative;
}

.side__item::before {
	content: "";
	display: block;
	padding-top: calc(100% + 1px);
}

.side__link {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	color: #262626;
	white-space: nowrap;
	text-decoration: none;
}

.side__inner {
	background: none;
	position: absolute;
	top: 0;
	left: 100%;
	height: 100%;
	font-size: 0;
	letter-spacing: -4px;
	white-space: nowrap;
	pointer-events: none;
	background: white;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
