/*lead */
.lead {
	position: relative;
	background-color: white;
	overflow: hidden;
	margin-bottom: 3rem;
}

.lead:last-child {
	margin-bottom: 0;
}

.lead__description {
	padding: 17px 20px;
	border-bottom: 1px solid #d9d9d9;
	overflow: hidden;
}

.lead__description p:last-child {
	margin-bottom: 0;
}

.lead__list {
	margin-left: -1px;
	margin-bottom: -1px;
	margin-right: -1px;
}

.lead__list::after {
	content: '';
	display: table;
	clear: both;
}

.lead__item {
	position: relative;
	float: left;
	width: 33.33%;
	padding: 0 0 1px 1px;
	text-align: center;
}

@media (max-width: 639px) {
	.lead__item {
		width: 100%;
	}
}

.lead__layer, .lead__layer--front, .lead__layer--back {
	position: relative;
	padding: 17px 20px 80px;
	background: white;
	box-shadow: -1px 0 0 #d9d9d9, 1px 0 0 #d9d9d9, 0 1px 0 #d9d9d9, 0 -1px 0 #d9d9d9;
	min-height: 100%;
	transition-property: opacity, visibiltiy;
	transition-duration: 0.2s;
}

.fb-switcher-active .lead__layer--front {
	opacity: 0;
	visibility: hidden;
}

.lead__layer--back {
	position: absolute;
	left: 1px;
	top: 0;
	right: 0;
	bottom: 1px;
	background-color: #fff;
	min-height: calc(100% - 1px);
	opacity: 0;
	visibility: hidden;
}

.no-js .lead__layer--back {
	opacity: 1;
	visibility: visible;
	position: relative;
	left: auto;
	top: auto;
	right: auto;
	bottom: auto;
}

.fb-switcher-active .lead__layer--back {
	opacity: 1;
	visibility: visible;
}

.lead__position {
	font-size: 1.6rem;
	line-height: 1.2;
	color: #999;
	margin-bottom: 1.5rem;
}

.lead__name {
	font-family: "Roboto Slab", serif;
	font-weight: bold;
	font-size: 1.8rem;
	line-height: 1.3;
	margin-bottom: 1.5rem;
}

.lead__footer {
	position: absolute;
	left: 0;
	width: 100%;
	bottom: 17px;
}

.no-js .lead__footer {
	display: none;
}

.lead__footer a {
	color: #006ab8;
	text-decoration: none;
}

.lead__footer a span {
	border-bottom: 1px dotted #006ab8;
	transition-property: border-color;
	transition-duration: 0.2s;
}

.no-touchevents .lead__footer a:hover span {
	border-bottom-color: transparent;
}

.lead__schedule {
	text-align: left;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.lead__schedule dl {
	padding: 0;
	margin: 0 0 1.5rem;
}

.lead__schedule dl:last-child {
	margin-bottom: 0;
}

.lead__schedule dl dt, .lead__schedule dl dd {
	margin: 0;
	padding: 0;
}

.lead__schedule dl dt {
	float: left;
	margin-right: 6px;
	font-family: "Roboto", sans-serif;
	font-weight: 700;
}

/*lead end */
/*history info */
.history-info::after {
	content: '';
	display: table;
	clear: both;
}

.history-info__holder {
	background-color: white;
	padding: 80px;
}

@media (max-width: 1919px) {
	.history-info__holder {
		padding: 30px;
	}
}

@media (max-width: 959px) {
	.history-info__holder {
		padding: 25px;
	}
}

@media (max-width: 639px) {
	.history-info__holder {
		padding: 15px;
	}
}

.history-info__img {
	float: right;
	border: 10px solid #e5e5e5;
	margin-left: 80px;
}

@media (max-width: 1599px) {
	.history-info__img {
		border-width: 6px;
		margin-left: 25px;
		width: 36%;
	}
}

@media (max-width: 479px) {
	.history-info__img {
		display: block;
		float: none;
		margin-right: auto;
		margin-left: auto;
		width: 240px;
		max-width: 100%;
		margin-bottom: 3rem;
	}
}

.history-info__text {
	overflow: hidden;
	font-size: 1.6rem;
	line-height: 1.875;
	margin-bottom: 3rem;
	max-height: 27rem;
}

.no-js .history-info__text {
	max-height: none;
}

.history-info__text p:last-child {
	margin-bottom: 0;
}

.text-slide__button .btn-arrow--bottom.active > span::before {
	transform: rotate(-45deg);
}

.text-slide__button .btn-arrow--bottom.active > span::after {
	transform: rotate(45deg);
}

/*history info end */
/*media block */
.media-block {
	margin-bottom: 3rem;
}

.media-block:last-child {
	margin-bottom: 0;
}

.media-block__nav {
	border-bottom: 1px solid #aeaeae;
	margin-bottom: 20px;
}

.media-block__nav::after {
	content: '';
	display: table;
	clear: both;
}

@media (max-width: 479px) {
	.media-block__nav {
		display: none;
	}
}

.media-block__tab {
	float: left;
	margin-bottom: -1px;
}

.media-block__tab a {
	display: inline-block;
	vertical-align: top;
	padding: 12px 20px;
	font-family: "Roboto Slab", serif;
	font-weight: 400;
	font-size: 1.2rem;
	line-height: 1.5;
	text-decoration: none;
	color: #999;
	text-transform: uppercase;
	border: 1px solid transparent;
	border-bottom-color: #aeaeae;
	background-color: #e5e5e5;
	transition-property: border-color, color;
	transition-duration: 0.2s;
}

@media (max-width: 479px) {
	.media-block__tab a {
		font-size: 1.0rem;
		padding: 10px 8px;
	}
}

.no-touchevents .media-block__tab a:hover {
	color: #006ab8;
}

.media-block__tab a.active-tab, .no-touchevents .media-block__tab a.active-tab:hover {
	border-color: #aeaeae;
	border-bottom-color: transparent;
	color: #262626;
	cursor: default;
}

.media-block__tab:first-child a, .no-touchevents .media-block__tab:first-child a:hover {
	border-left-color: transparent;
}

.media-block__panel {
	overflow: hidden;
}

@media (max-width: 479px) {
	.media-block__panel {
		opacity: 1 !important;
		visibility: visible !important;
		position: relative !important;
		left: auto !important;
		top: auto !important;
		margin-bottom: 3rem;
	}
	.media-block__panel:last-child {
		margin-bottom: 0;
	}
}

.media-block__panel .section__subtitle {
	display: none;
}

@media (max-width: 479px) {
	.media-block__panel .section__subtitle {
		display: block;
	}
}

.media-list {
	margin-bottom: 3rem;
	margin-left: -10px;
	margin-right: -10px;
	font-size: 0;
	letter-spacing: -4px;
}

.media-list:last-child {
	margin-bottom: 0;
}

.media-block__panel .media-list {
	margin-bottom: -20px;
}

@media (max-width: 479px) {
	.media-list {
		margin-left: -1px;
		margin-right: -1px;
	}
	.media-block__panel .media-list {
		margin-bottom: -2px;
	}
}

.media-list__item {
	display: inline-block;
	vertical-align: top;
	width: 25%;
	padding-left: 10px;
	padding-right: 10px;
	margin-bottom: 20px;
	font-size: 1.4rem;
	line-height: 1.3;
	letter-spacing: normal;
	text-align: center;
}

@media (max-width: 1599px) {
	.media-list__item {
		display: inline-block;
		vertical-align: top;
		width: 50%;
		padding-left: 10px;
		padding-right: 10px;
		margin-bottom: 20px;
	}
}

@media (max-width: 479px) {
	.media-list__item {
		display: inline-block;
		vertical-align: top;
		width: 50%;
		padding-left: 1px;
		padding-right: 1px;
		margin-bottom: 2px;
	}
}

.media-list__inner {
	display: block;
	height: 100%;
	text-decoration: none;
	color: #262626;
	padding: 10px;
	background-color: white;
	transition-property: color;
	transition-duration: 0.2s;
}

.no-touchevents .media-list__inner:hover {
	color: #006ab8;
}

.media-list__img {
	position: relative;
	overflow: hidden;
	margin-bottom: 1.5rem;
}

.media-list__figure {
	position: relative;
	padding-top: 100%;
	transition-property: transform, opacity;
	transition-duration: 0.2s;
}

.media-list__figure img {
	position: absolute;
	left: 50%;
	top: 50%;
	max-width: 100%;
	max-height: none;
	min-width: 0;
	min-height: 0;
	width: calc(100% + 4px);
	height: calc(100% + 4px);
	transform: translate(-50%, -50%);
	text-align: left;
	object-fit: cover;
	backface-visibility: hidden;
}

.no-objectfit .media-list__figure img {
	width: auto;
	height: 100%;
}

.media-list__item .video-play {
	background-color: rgba(0, 0, 0, 0.1);
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	position: absolute;
	transition: background-color 0.15s ease 0s;
}

.media-list__item .video-play img {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 50px;
	height: 50px;
	transform: translate(-50%, -50%);
}

.media-list__title {
	padding-bottom: 5px;
}

.popup-gallery {
	display: none;
}

/*media block end */
/*survey */
.survey {
	padding: 17px 20px;
	background-color: #006ab8;
	color: white;
}

.survey__header {
	margin-bottom: 20px;
	font-size: 1.4rem;
	line-height: 1.5;
	color: rgba(255, 255, 255, 0.51);
}

.survey__header::after {
	content: '';
	display: table;
	clear: both;
}

.survey__header > span {
	display: block;
	overflow: hidden;
}

.survey__all {
	float: right;
	margin-left: 15px;
	color: inherit;
}

.survey__title {
	text-align: center;
	margin-bottom: 30px;
	font-family: "Roboto Slab", serif;
	font-size: 1.6rem;
	line-height: 1.2;
	color: white;
}

.survey__answers, .user-content .survey__answers {
	margin-bottom: 30px;
}

.survey__answers:last-child, .user-content .survey__answers:last-child {
	margin-bottom: 0;
}

.survey__answers li {
	margin: 2rem 0;
}

.survey__answers {
	line-height: 1;
	min-height: 188px;
}

.survey__answers input[type="checkbox"] + label,
.survey__answers input[type="radio"] + label,
.survey__answers .check-label,
.survey__answers .radio-label {
	font-size: 14px;
	line-height: 18px;
	color: white;
}

.survey__answers input[type="checkbox"] + label::before,
.survey__answers input[type="checkbox"] + span i::before,
.survey__answers input[type="radio"] + label::before,
.survey__answers input[type="radio"] + span i::before {
	width: 20px;
	height: 20px;
	border-width: 0;
	background-color: white;
}

.survey__answers input[type="checkbox"] + label span,
.survey__answers input[type="checkbox"] + span,
.survey__answers input[type="radio"] + label span,
.survey__answers input[type="radio"] + span {
	padding: 1px 0 1px 35px;
}

.survey__answers input[type="checkbox"] + label span::before, .survey__answers input[type="checkbox"] + label span::after,
.survey__answers input[type="checkbox"] + span::before,
.survey__answers input[type="checkbox"] + span::after {
	width: 0;
	height: 4px;
	top: 10px;
}

.survey__answers input[type="checkbox"] + label span::after,
.survey__answers input[type="checkbox"] + span::after {
	left: 22px;
}

.survey__answers input[type="checkbox"]:checked + label span::before, .survey__answers input[type="checkbox"]:checked + label span::after,
.survey__answers input[type="checkbox"]:checked + span::before,
.survey__answers input[type="checkbox"]:checked + span::after {
	background-color: #ec3c01;
}

.survey__answers input[type="checkbox"]:checked + label span::before,
.survey__answers input[type="checkbox"]:checked + span::before {
	width: 9px;
	margin: -5px 0 0 0;
}

.survey__answers input[type="checkbox"]:checked + label span::after,
.survey__answers input[type="checkbox"]:checked + span::after {
	width: 12px;
	margin: 0 0 0 -8px;
}

.survey__answers input[type="checkbox"]:disabled + label span::before, .survey__answers input[type="checkbox"]:disabled + label span::after,
.survey__answers input[type="checkbox"]:disabled + span::before,
.survey__answers input[type="checkbox"]:disabled + span::after {
	background: #737373;
}

.survey__answers input[type="radio"] + label span::after,
.survey__answers input[type="radio"] + span i::after {
	width: 12px;
	height: 12px;
	top: 4px;
	left: 4px;
	background: #ec3c01;
}

.survey__answers input[type="radio"]:disabled + label,
.survey__answers input[type="radio"]:disabled + span {
	color: white;
}

.survey__answers input[type="radio"]:disabled + label span::after,
.survey__answers input[type="radio"]:disabled + span i::after {
	background: #262626;
}

.survey__footer {
	display: table;
	width: 100%;
	height: 40px;
}

.survey__footer a {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	color: rgba(255, 255, 255, 0.51);
}

.survey__footer [class*="btn-default-"] {
	padding: 8px;
}

/*survey end */
/*faq */
/*expander */
.expander {
	margin-bottom: 6rem;
}

@media (max-width: 639px) {
	.expander {
		margin-right: -15px;
		margin-left: -15px;
	}
}

.expander:last-child {
	margin-bottom: 0;
}

.expander-item {
	border: 1px solid #d9d9d9;
	margin-bottom: 3rem;
	padding: 0 20px;
}

@media (max-width: 959px) {
	.expander-item {
		padding-right: 25px;
		padding-left: 25px;
	}
}

@media (max-width: 639px) {
	.expander-item {
		padding-right: 15px;
		padding-left: 15px;
		margin-bottom: 0;
		border-width: 0 0 1px;
	}
	.expander-item:first-child {
		border-top-width: 1px;
	}
}

.expander-item:last-child {
	margin-bottom: 0;
}

.expander-item p, .expander-item ul, .expander-item ol {
	max-width: none;
}

.user-content .expander-item ul > li::before {
	top: calc(1em * 1.25 / 2);
}

.expander-header {
	position: relative;
	padding: 17px 0;
	transition-property: border-color;
	transition-duration: 0.2s;
}

.expander-header::after {
	content: '';
	display: table;
	clear: both;
}

@media (max-width: 959px) {
	.expander-header {
		padding-top: 20px;
		padding-bottom: 20px;
	}
}

@media (max-width: 639px) {
	.expander-header {
		padding-top: 12px;
		padding-bottom: 12px;
	}
}

.expander-header__meta {
	font-size: 1.2rem;
	line-height: 1.7;
	color: #999;
	margin-bottom: 3rem;
}

@media (max-width: 1599px) {
	.expander-header__meta {
		display: block;
		float: none;
		width: auto;
		margin-bottom: 1.5rem;
	}
}

@media (max-width: 639px) {
	.expander-header__meta {
		font-size: 1.6rem;
		line-height: 1.88;
	}
}

.expander-header__meta time {
	font-family: "Roboto", sans-serif;
	font-weight: 300;
}

@media (max-width: 639px) {
	.expander-header__meta time {
		display: inline-block;
		margin-right: 20px;
	}
}

.expander-header__meta .name {
	display: inline-block;
	margin-right: 8px;
}

.expander-header__meta .name ~ time::before {
	content: "/";
	display: inline-block;
	position: relative;
	margin-right: 8px;
}

.expander-header__text {
	font-family: "Roboto Slab", serif;
	font-size: 1.6rem;
	line-height: 1.25;
	font-weight: normal;
	color: #262626;
	transition-property: color;
	transition-duration: 0.2s;
	margin-bottom: 3rem;
}

.expander-angle {
	display: inline-block;
	vertical-align: top;
	position: relative;
	cursor: pointer;
	line-height: 18px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.no-js .expander-angle {
	display: none;
}

.expander-angle > span {
	display: inline-block;
	vertical-align: top;
	position: relative;
	font-family: "Roboto Slab", serif;
	text-transform: uppercase;
	font-size: 0;
	line-height: 0;
	font-weight: normal;
	white-space: nowrap;
}

.expander-angle > span:before, .expander-angle > span:after {
	display: inline-block;
	vertical-align: top;
	font-size: 12px;
	line-height: 18px;
	position: relative;
	border-bottom: 1px dotted #262626;
	transition-property: opacity, transform, visibility;
	transition-duration: 0.2s;
}

.expander-angle > span:before {
	content: 'Показать ответ';
	transform: translate(0, 0);
}

.expander-is-open .expander-angle > span:before {
	opacity: 0;
	visibility: hidden;
	transform: translate(0, -1em);
}

.expander-angle > span:after {
	content: 'Закрыть ответ';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	visibility: hidden;
	width: auto;
	transform: translate(0, 1em);
}

.expander-is-open .expander-angle > span:after {
	opacity: 1;
	visibility: visible;
	transform: translate(0, 0);
}

.no-touchevents .expander-angle > span:hover {
	color: #006ab8;
}

.no-touchevents .expander-angle > span:hover:before, .no-touchevents .expander-angle > span:hover:after {
	border-bottom-color: #006ab8;
}

.expander-angle > i {
	display: none;
}

.expander-panel {
	position: relative;
	overflow: hidden;
	border-top: 1px solid #d9d9d9;
	display: none;
	font-size: 1.4rem;
	line-height: 1.43;
}

.no-js .expander-panel {
	display: block;
}

.expander-panel__holder {
	padding: 17px 0;
}

@media (max-width: 959px) {
	.expander-panel__holder {
		padding-top: 20px;
		padding-bottom: 20px;
	}
}

@media (max-width: 639px) {
	.expander-panel__holder {
		padding-top: 12px;
		padding-bottom: 12px;
	}
}

/*expander end */
/*faq form */
.faq-form-wrap {
	margin-bottom: 6rem;
}

.faq-form-wrap:last-child {
	margin-bottom: 0;
}

/*faq form end */
/*faq end */
/*breadcrumbs*/
.home-page .breadcrumbs {
	display: none;
}

.breadcrumbs {
	position: relative;
	font-size: 1.4rem;
	line-height: 21px;
	color: #262626;
	margin-bottom: 3rem;
	cursor: default;
	z-index: 35;
}

.breadcrumbs::after {
	content: '';
	display: table;
	clear: both;
}

@media (max-width: 1919px) {
	.breadcrumbs {
		margin-bottom: 0.5rem;
	}
}

.breadcrumbs__list::after {
	content: '';
	display: table;
	clear: both;
}

.breadcrumbs__item {
	float: left;
	position: relative;
	margin: 0 10px 1rem 0;
	padding: 0;
}

@media (max-width: 1919px) {
	.breadcrumbs__item {
		margin-bottom: 0.5rem;
	}
}

.breadcrumbs__item:first-child {
	margin-left: 0;
}

.breadcrumbs__item > span {
	display: inline-block;
	vertical-align: top;
	max-width: 320px;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

@media (max-width: 639px) {
	.breadcrumbs__item > span {
		max-width: 240px;
	}
}

.breadcrumbs__item > a {
	display: inline-block;
	vertical-align: top;
	position: relative;
	padding: 0 18px 0 0;
	color: #006ab8;
	text-decoration: none;
	min-width: 0;
	cursor: pointer;
	transition-property: max-width, opacity, visibility, border-color;
	transition-duration: 0.2s;
}

.breadcrumbs__item > a > span {
	display: block;
	position: relative;
	max-width: 320px;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	border-bottom: 1px solid transparent;
	z-index: 5;
	transition-property: border-color;
	transition-duration: 0.2s;
}

@media (max-width: 639px) {
	.breadcrumbs__item > a > span {
		max-width: 240px;
	}
}

.no-touchevents .breadcrumbs__item > a:hover > span {
	border-bottom-color: #99c3e3;
}

.breadcrumbs__item > a > i {
	position: absolute;
	right: 0;
	top: 7px;
	width: 7px;
	height: 8px;
	pointer-events: none;
}

.breadcrumbs__item > a > i::before {
	content: '';
	position: absolute;
	left: 0;
	top: 50%;
	height: 0;
	width: 0;
	border: solid transparent;
	border-width: 4px 0 4px 7px;
	margin-top: -4px;
	border-left-color: #ec3c01;
}

.breadcrumbs__item > i {
