.fotorama .fotorama__arr {
	top: 0;
	width: 50px;
	height: 100%;
	display: block;
	margin-top: 0;
	position: absolute;
	background: none;
	outline: none;
	color: white;
}

@media (max-width: 639px) {
	.fotorama .fotorama__arr {
		width: 40px;
	}
}

.fotorama .fotorama__arr:hover {
	opacity: 1;
}

.fotorama .fotorama__arr.fotorama__arr--disabled {
	opacity: 0.25;
	cursor: default;
}

.fotorama .fotorama__arr::before {
	content: "";
	display: block;
	position: absolute;
	width: 50px;
	height: 50px;
	top: 50%;
	transform: translate(0, -50%);
	background-color: #006ab8;
}

@media (max-width: 639px) {
	.fotorama .fotorama__arr::before {
		width: 40px;
		height: 40px;
		background-color: rgba(0, 106, 184, 0.8);
	}
}

.fotorama .fotorama__arr::after {
	content: "";
	display: block;
	position: absolute;
	top: 50%;
	opacity: 1;
	pointer-events: none;
	width: 12px;
	height: 12px;
	transform: rotate(45deg);
	border: 0 solid white;
}

.fotorama .fotorama__arr--prev {
	left: 0;
}

.fotorama .fotorama__arr--prev::after {
	left: 50%;
	border-width: 0 0 2px 2px;
	margin: -6px 0 0 -4px;
}

.fotorama .fotorama__arr--next {
	right: 0;
}

.fotorama .fotorama__arr--next::after {
	right: 50%;
	border-width: 2px 2px 0 0;
	margin: -6px -4px 0 0;
}

.fotorama__fullscreen-icon {
	width: auto;
	height: 50px;
	top: 0;
	right: 0;
	background-image: url(../img/zoom.svg);
	background-color: rgba(0, 0, 0, 0.36);
	background-position: 20px 50%;
	background-size: 20px;
	padding: 15px 20px 15px 50px;
	font-family: 'Roboto Slab', sans-serif;
	font-weight: 400;
	font-style: normal;
	font-size: 16px;
	line-height: 20px;
	color: white;
	z-index: 20;
}

@media (max-width: 1599px) {
	.fotorama__fullscreen-icon {
		font-size: 0;
		line-height: 0;
		padding: 0;
		width: 50px;
		height: 50px;
		background-position: 50% 50%;
	}
}

@media (max-width: 639px) {
	.fotorama__fullscreen-icon {
		width: 40px;
		height: 40px;
	}
}

.fotorama__fullscreen-icon::before {
	content: "Увеличить фото";
}

