	content: "Enlarge picture";
}

.fotorama--fullscreen .fotorama__fullscreen-icon {
	background-image: url(../img/close.svg);
	background-position: 50% 50%;
	background-size: 20px;
	width: 50px;
	height: 50px;
	padding: 0;
}

