@import "../utils/mixins";

@media print {
	*,
	*::before,
	*::after {
		background: transparent !important;
		color: black !important;
		box-shadow: none !important;
		text-shadow: none !important; }

	a, a:visited {
		text-decoration: underline; }

	a[href^=http]::after {
		content: " <" attr(href) "> "; }

	a[href]::after {
		content: " (" attr(href) ")"; }

	abbr[title]::after {
		content: " (" attr(title) ")"; }

	a[href^="#"]::after, a[href^="javascript:"]::after {
		content: ""; }

	pre, blockquote {
		border: 1px solid #999;
		page-break-inside: avoid; }

	thead {
		display: table-header-group; }

	tr, img {
		page-break-inside: avoid; }

	img {
		max-width: 100% !important;
		filter: grayscale(1); }

	p, h2, h3 {
		orphans: 3;
		widows: 3; }

	h2, h3 {
		page-break-after: avoid; }

	hr {
		border-color: black; }

	body {
		font-family: Arial, Helvetica, sans-serif;
		font-size: 10pt;
		line-height: 1.5 !important; }

	h1 {
		font-size: 20pt !important;
		line-height: 1.3 !important; }

	h2 {
		font-size: 18pt !important;
		line-height: 1.4 !important; }

	h3, .main-slider-title__item, .section__title {
		font-size: 16pt !important;
		line-height: 1.5 !important; }

	h4, blockquote q {
		font-size: 14pt !important;
		line-height: 1.5 !important; }

	h5, blockquote cite {
		font-size: 12pt !important;
		line-height: 1.6 !important; }

	h6 {
		font-size: 8pt !important;
		line-height: 1.8 !important; }

	table td, table th, table {
		border: 1px solid black !important;
		font-size: 9pt !important;
		color: black !important;
		padding: 3pt !important;

		& > * {
			padding: 0!important; } }

	.main, .content, .layout, .layout-text, .layout-flood, .header, .header__holder, .blockquote, .blockquote *, blockquote, .imgl, .imgr, .imgc, .table-auto table, .header__frame, .logo-wrap, .header__content, .header .holder, .header .frame, .header__info__list, .header__title, .header__info__list li, .main-inside, .main-holder, .content-layout, .article-meta, .sheet, .section, .files, .files__item, .files__inner, .files__depict, .files__description, .files__meta, .files__meta > span, .news-previews, .news-previews__list, .news-previews__item, .news-previews__content, .news-previews__inner, .news-previews__text, .news-previews__content::before, .footer, .footer__holder, .footer-bottom, .footer-bottom__col, .grid-row, [class*="grid-col-"], .save, .save__holder, .save__box, .save__align, .events-sum, .events-sum__panels, .events-sum__panel, .events-list, .events-list__item, .section__holder, .news-grid, .news-grid__list, [class*="news-grid__item"], .news-grid__inner, .news-grid__content, .news-grid__title, .news-grid__text, .news-grid__meta, .section__subtitle h2, .section__subtitle h3, .section__subtitle h4, .news-group, .news-group__list, .section__box, .news-group__item, .news-group__content, .news-group__inner, .news-group__text, .news-group__meta, .soc-media__col, .soc-media__text, .soc-media__link, .logo a,.logo img, .header__title > a, .news-float__list, .news-float__item, .news-float__content, .news-float__title, .news-float__text, .news-float__meta, .extra-popup-opened [class*="popup"][style*="opacity: 1"], .expander-item, .expander-header, .expander-header__meta, .expander-header__text, .expander-panel, .expander-panel__holder, .fotorama, .fotorama__wrap, .fotorama__nav-wrap, .fotorama__nav, .fotorama__nav__shaft, .fotorama__nav__frame--thumb, .fotorama__thumb, .fotorama__img {
		@include print-reset; }

	.wrapper::before, .page-404 .sheet::before, .sidebar, .aside, .print-hidden, .view-print, .nav, .nav-list, .breadcrumbs, .breadcrumbs__list, form, .spacer, [class*="btn-"], .side, .header__top, .header__contacts, .header__info__list li svg, .header__info__list li a::after, .user-form, .ctrl, .news-previews__img, .news__label, .banners-panel, .footer-gov, .footer-common, .news-previews__footer, .developer__logo, .promo, .years, .events-sum__nav, .events-sum__footer, .news-grid__img, .news__label_video, .news-grid__slider, #multimedia-anchor, .tape-slider, .v-separator, .news-group::before, .news-group__img, .soc-media .soc-media__col:first-child, .soc-media::before, .soc-media__img, .logo-short-text, .header__content .logo, .go-to-mchs, .news-float__img, .filter-item, .extra-popup-opened .wrapper, .extra-popup-opened .footer, .expander-angle, .page-nav, .header__title a::before, .header__title a::after, .logo::after, .fotorama__stage, .fotorama__thumb-border, .info-map {
		display: none !important;
		content: none !important; }

	h1, h2, h3, h4, h5, h6, p, ul, ol, .table-auto, table, .imgl, .imgr, .imgc, .datetime, .files, .files__item, .news-previews__item, .save__box, .events-list__item, .news-grid__title, [class*="news-grid__item"], .news-group__item, .soc-media__text, .news-float__item, .expander-header, .expander-header__meta {
		@include prints-space-bottom-sm;
		max-width: none !important; }

	.header, .blockquote, hr, blockquote, .section, .save, .events-sum, .section__box, .expander-item {
		@include prints-space-bottom-md; }

	.logo-wrap {
		position: relative !important;
		float: left !important;
		background: none !important;
		height: 2.5cm !important;
		width: 2.5cm !important;
		margin-right: 0.5cm !important;

		a {
			display: block !important; }

		& > *::after {
			display: none !important;
			content: none !important; }

		img {
			width: 100% !important;
			display: block !important;
			float: left !important; } }

	.header__frame {
		overflow: hidden !important; }

	blockquote q::before, blockquote q::after {
		padding: 0 !important;
		margin: 0 !important;
		font-size: 18pt !important;
		display: inline-block !important; }

	.article-layout {
		margin-left: 0 !important;
		margin-right: 0 !important;
		padding-left: 0 !important;
		padding-right: 0 !important;
		padding-top: 0 !important; }

	.article-layout .full-width {
		margin-right: 0 !important;
		margin-left: 0 !important; }

	.files__depict {
		width: 1cm !important;
		float: left !important;
		margin-right: 0.5cm !important;
		margin-bottom: 0.5cm !important; }

	.files__description {
		margin-left: 1.5cm !important; }

	.news-previews__meta, .news-grid__meta, .news-group__meta {
		width: 5cm !important; }

	.expander-panel {

		&::before {
			content: 'Ответ:';
			display: inline-block;
			vertical-align: top;
			font-size: 15pt;
			font-weight: bold !important;
			margin-bottom: 0.5cm !important; } }

	/*js elements */
	.slick-slider, .slick-slider, .slick-list, .slick-track, .slick-slide, .flashes, .flashes * {
		@include print-reset;

		::before, ::after {
			display: none !important;
			content: none !important; } }

	.images-slider .slick-slide:first-child ~ .slick-slide, .slider-counter, .slick-arrow, .slick-dots {
		display: none !important;
		content: none !important; }

	.slick-slide, .flashes__item {
		@include prints-space-bottom-sm; }

	.swiper-wrapper {
		display: block;
		transform: none !important;
		white-space: normal; }

	.fotorama__nav__frame--thumb {
		display: inline-block !important;
		vertical-align: top !important;
		margin-right: 0.5cm !important;
		margin-bottom: 0.5cm !important; }

	.fotorama__img {
		width: 5cm !important; } }
