@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*sliders*/
// slider arrows
.slick-arrow, .swiper-arrow {
	@include size(40px);
	display: block;
	margin-top: 0;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transform: translate(0, -50%);
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;
	@include font(0, 0);
	color: transparent;

	.no-js & {
		display: none; }

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, &.swiper-button-disabled, .no-touchevents &.slick-disabled, .no-touchevents &.swiper-button-disabled {
		opacity: 0.25;
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(12px);
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev, .swiper-button-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next, .swiper-button-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

.swiper-button-disabled {
	pointer-events: auto; }

// arrows outside
.slick-arrow-outside, .slick-arrow-outside:hover, .slick-arrow-outside:focus {
	background-color: $btn-bg-color; }

// переопределяем стили для ховера и фокуса на стрелки слайдера
.slick-arrow-outside {
	left: auto;
	@include size(30px);

	.no-touchevents &:hover, .no-touchevents &:focus {
		background-color: $btn-bg-color-hover;

		&.slick-disabled {
			background-color: $btn-bg-color; } }

	&::after {
		border-color: white; } }

.slick-prev-outside {
	right: (30px + 10); }

.slick-next-outside {
	right: 0; }


// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.content & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&,
		.content & {
			padding: 4px 10px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 12px;
			height: 12px;
			padding: 0;
			border-radius: 50%;
			background: $text-color-alt;
			position: relative;
			transition-property: background, border, box-shadow, transform;
			transition-duration: 0.6s;

			&::before {
				content: '';
				position: absolute;
				left: 2px;
				top: 2px;
				height: 8px;
				width: 8px;
				background: #fff;
				border-radius: 50%;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
				transition-property: transform, opacity;
				transition-duration: 0.6s; } }

		//.no-touchevents &:hover button::before

		//&.slick-active button::before,
 } }		//.no-touchevents &.slick-active:hover button::before
/*sliders end*/

/*images slider*/
.images-slider {
	position: relative;
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space; } }

.images-slider__list {
	padding-bottom: $bottom-space;
	position: relative;
	z-index: 2;

	@include media('<tab-lg') {
		padding-bottom: 0;
		margin-bottom: $bottom-space; } }

.images-slider__item {
	float: none;
	display: inline-block;
	vertical-align: top;
	opacity: 0;
	white-space: normal;
	overflow: hidden;
	position: relative;
	background: #494949;
	cursor: url(../../img/cursor-zoom.png), zoom-in;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1; }

	&::before {
		content: '';
		display: block;
		height: 0;
		padding-top: 62.5%; }

	img {
		position: absolute;
		top: -2px;
		left: 50%;
		text-align: center;
		max-width: none;
		height: calc(100% + 4px);
		transform: translate(-50%, 0); } }

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0);
		background-color: rgba(255, 255, 255, 0.4);

		@include media('<tab-lg') {
			top: auto;
			bottom: 0; } }

	.slick-prev {
		right: 40px; }

	// slider counter
	.slider-counter {
		position: absolute;
		left: 0;
		top: 100%;
		color: $text-color-light;

		.slide-curr {
			color: $text-color; }

		@include media('<tab-lg') {
			top: 0;
			color: white;
			text-shadow: 1px 1px 2px black;
			background-color: black;
			padding: 0 6px;

			.slide-curr {
				color: white; } } }

	& ~ br {
		display: none; } }

/*images slider end*/

/*flashes*/
.flashes {
	padding: 0 90px;

	@include media('<desk-lg') {
		padding: 0 90px 0 50px; }

	@include media('<tab-lg') {
		padding: 0; } }

.flashes__item {
	overflow: hidden;
	display: none;

	&:first-child {
		display: block; } }

.flashes__content {
	box-sizing: border-box;

	& > p:last-child,
	& > ul:last-child,
	& > ol:last-child {
		margin-bottom: 0;
		padding-bottom: 0; } }

/*flashes end*/

/*promo slider*/
.promo {
	margin-bottom: $bottom-space-md;
	@include push-auto;

	@include media('<mob-lg') {
		margin-bottom: 20px; } }

.promo-slider {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end
	@include push-auto;
	margin-bottom: 0;

	@include media('<desk-hd') {
 }		//max-width: $section-layout-width-desk-hd

	@include media('<desk-lg') {
 }		//max-width: $section-layout-width-desk-lg

	@include media('<tab-lg') {
 }		//max-width: $section-layout-width-tab-lg

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 }		// after slide load end

	&.slick-dotted {
		margin-bottom: 0; }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: 20px;

		li {
			@include padding-horizontal(7px);

			button {
				@include size(6px);
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition-property: opacity, transform;
				transition-duration: $animation-speed;

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

.promo-slider__img {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end
	position: relative;
	height: 300px;
	width: 100%;
	padding: $article-layout-space-vertical $article-layout-space-horizontal;
	text-decoration: none;
	color: white;
	background-color: #ddd;
	background-position: center center;
	background-repeat: no-repeat;

	background-size: cover;

	@include media('<desk-hd') {
		padding: $article-layout-space-vertical--desk-hd $article-layout-space-horizontal--desk-hd; }

	@include media('<tab-lg') {
		padding: $article-layout-space-vertical--tab-lg $article-layout-space-horizontal--tab-lg; }

	@include media('<mob-lg') {
		padding: $article-layout-space-vertical--mob-lg $article-layout-space-horizontal--mob-lg;
		height: 240px; }

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.promo-slider__figure {
	position: absolute;
	display: block;
	left: 50%;
	top: 50%;
	width: 48px;
	height: 48px;
	margin: -24px 0 0 -24px;

	&.loaded {
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		max-width: none;
		margin: 0;
		@include size(100%);
		@include object-fit(cover); } }

.promo-slider__text {
	position: relative;
	overflow: hidden;
	height: calc(3 * #{$font-size-h1} * #{$line-height-h1});
	font-family: $font-alt;
	font-weight: 700;
	@include font($font-size-h1, $line-height-h1);
	text-shadow: 0 1px 1px rgba(0, 0, 0, 0.51);
	width: 930px;
	max-width: 100%;

	@include media('<desk-hd') {
		$fz-kef: 0.8;
		$lh-kef: 1.2;

		height: calc(3 * #{round-float($font-size-h1*$fz-kef, 1)} * #{round-float($line-height-h1*$lh-kef, 1)});
		@include font(round-float($font-size-h1*$fz-kef, 1), round-float($line-height-h1*$lh-kef, 1)); }

	@include media('<desk-lg') {
		$fz-kef: 0.6;
		$lh-kef: 1.3;

		height: calc(4 * #{round-float($font-size-h1*$fz-kef, 1)} * #{round-float($line-height-h1*$lh-kef, 1)});
		@include font(round-float($font-size-h1*$fz-kef, 1), round-float($line-height-h1*$lh-kef, 1)); }

	@include media('<mob-lg') {
		$fz-kef: 0.5;
		$lh-kef: 1.3;

		height: calc(4 * #{round-float($font-size-h1*$fz-kef, 1)} * #{round-float($line-height-h1*$lh-kef, 1)});
		@include font(round-float($font-size-h1*$fz-kef, 1), round-float($line-height-h1*$lh-kef, 1)); } }

.promo-slider__footer {
	position: relative;

	.promo-slider__text ~ & {
		padding-top: $bottom-space;

		@include media('<tab-lg') {
			padding-top: $bottom-space-sm; } } }

/*promo slider end*/

/*news slider*/
.news-grid__slider {
	position: relative; }

.news-grid-slider {
	// before slide load
	white-space: nowrap;
	opacity: 0;
	// before slide load end
	background-color: white;
	margin-bottom: 0;
	position: relative;
	overflow: hidden; // обрезает стрелки при увеличении на ховер

	&.slick-initialized, .no-js & {
		// before slide load
		white-space: normal;
		opacity: 1;
 }		// before slide load end

	&.slick-dotted {
		margin-bottom: 0; } }

.news-grid__slider .news-grid-slider .news-grid-slider__slide {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 }		// after slide load end

	.news-grid__inner {
		outline: none; } }

.news-grid-thumbs {
	$thumb-size: 60px;
	//============
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	position: absolute;
	top: $news-item-lg-img-height;
	margin-top: -$thumb-size;
	left: 0;
	width: 100%;
	padding-top: $thumb-size;
	z-index: 5;

	.slider-inited &, .no-js & {
		opacity: 1; }

	@include media('<desk-lg') {
		top: 0;
		padding-top: 56.25%;
		margin-top: 0; }

	&::before {
		@include pseudo();
		visibility: visible;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 120px;
		@include linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.12) 17%,rgba(0,0,0,0.58) 61%,rgba(0,0,0,0.69) 73%,rgba(0,0,0,0.75) 83%,rgba(0,0,0,0.75) 100%);

		.no-js & {
			content: none; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		top: auto;
		bottom: 0;
		margin: -20px 0 20px;
		transform: none;
		z-index: 5;

		li {
			visibility: visible;
			pointer-events: auto;
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition-property: opacity, transform;
				transition-duration: $animation-speed;

				&::before {
					content: none; } } }

		li {
			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } }

	.slick-arrow {
		visibility: visible;
		pointer-events: auto;
		top: auto;
		bottom: 0;
		transform: none;
		margin-top: 0;
		width: $thumb-size;
		height: $thumb-size;
		z-index: 6;
		transition: {
			property: transfrom;
			duration: $animation-speed; }

		//+media('<desk-lg')
		//op: auto
		//ottom: 0
		//ransform: none
		//argin: 0

		//+media('<mob-lg')
		//op: $news-item-lg-img-height-mob-lg

		.no-touchevents &:hover {
			transform: scale(1.2); }

		.no-touchevents &:active {
			transform: scale(1); }

		&:hover {
			background: none; }

		&::after {
			border-color: white; } } }

.news-grid-slider__slide {
	height: $news-item-lg-img-height;
	width: 100%;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;

	@include media('<mob-lg') {
		height: $news-item-lg-img-height-mob-lg; }

	span {
		@include hide-text; }

	.home-page-01 & {
		height: auto; } }

/*news slider end*/

/*tape slider*/
.tape-slider {
	margin-bottom: $grid-space;
	border: 1px solid #b3b3b3;
	padding: 0 50px;
	position: relative;
	background-color: $bg-color;

	.tape-slider__list, .#{$user-content} .tape-slider__list {
		margin: 0;

		li {
			padding: 0;
			margin: 0;

			&::before {
				content: none; } } }

	.tape-slider__list {

		.no-js & {
			display: block; }

		li {
			display: inline-block;
			vertical-align: top;

			&.swiper-slide {
				width: auto;
				float: left; } } } }

.tape-slider__anchor {
	@include display-inline-block-top;
	padding: 14px 15px;
	text-decoration: none;
	font-family: $font-alt;
	font-weight: normal;
	@include font(1.2rem, 30px);
	color: $text-color;
	cursor: pointer;
	text-transform: uppercase;
	transition-property: color;
	transition-duration: $animation-speed;

	.no-touchevents &:hover, .no-touchevents .current &, .current & {
		color: $text-color-alt; } }

// swiper slider
.swiper-container {
	position: relative;
	transition-property: opacity, visibility;
	transition-duration: $animation-speed;

	.tape-slider & {
		&::before, &::after {
			content: '';
			position: absolute;
			top: 0;
			height: 100%;
			width: 15px;
			z-index: 2;
			pointer-events: none; } }

	&::before {
		left: 0;
		@include linear-gradient(to right, $bg-color 0%, transparentize($bg-color, 0.4) 50%, transparentize($bg-color, 0.9) 100%); }

	&::after {
		right: 0;
		@include linear-gradient(to left, $bg-color 0%, transparentize($bg-color, 0.4) 50%, transparentize($bg-color, 0.9) 100%); } }

//.tape-slider
//slick-prev
//left: -35px

//slick-next
//right: -35px

.tape-slider {
	.swiper-button-prev {
		left: 0; }

	.swiper-button-next {
		right: 0; } }


/*tape slider end*/

/*others news slider */
.others-news {
	.news-grid {
		overflow: hidden; }

	.news-grid__list:last-child {
		margin-bottom: -#{$grid-space};

		@include media('<mob-lg') {
			margin-bottom: 0; } }

	.section__footer {
		display: none; } }

/*others news slider end */

/*tape gallery */
.tape-gallery-wrap {
	margin-bottom: $bottom-space;
	text-align: center;

	&:last-child {
		margin-bottom: 0; } }

.tape-gallery {
	@include display-inline-block-top;
	max-width: 100%;
	text-align: left;
	overflow: hidden;
	background-color: white;

	.swiper-container {
		position: relative;
		width: 100%;
		height: auto;
		margin-left: auto;
		margin-right: auto; } }

.tape-gallery__list {
	font-size: 0;
	letter-spacing: -4px;
	white-space: nowrap;

	.swiper-slide {
		display: inline-block;
		vertical-align: top;
		padding: 0;
		font-size: 14px;
		line-height: 1.5;
		letter-spacing: normal;
		white-space: normal;
		box-sizing: border-box;
		width: auto;
		height: auto;
		opacity: 0.5;
		transition: {
			property: opacity;
			duration: $animation-speed; }

		&.swiper-slide-active {
			opacity: 1; }

		a {
			display: block;
			color: black;
			text-decoration: none;
			box-sizing: border-box;
			height: 100%;

			&:hover {
				text-decoration: none;
				cursor: url(../../img/cursor-zoom.png), zoom-out; } } } }

.tape-gallery__img {
	display: block;
	position: relative;
	overflow: hidden;
	background-color: #000;
	//height: 214px
	height: 480px;

	img {
		min-width: 0;
		min-height: 0;
		max-width: none;
		max-height: none;
		height: 100%;
		width: auto;
		backface-visibility: hidden;
		transition: {
			property: transform;
			duration: $animation-speed; } } }

//.tape-gallery__list a:hover .tape-gallery__img img
//pacity: 0.85
//ransform: scale(1.2)
//ransition-duration: 15s

.tape-gallery {
	.swiper-button-prev, .swiper-button-next {
		position: absolute;
		top: 0;
		bottom: 0;
		@include size(50px, auto);
		margin: 0;

		&::before {
			@include pseudo();
			@include centering(); }

		&.swiper-button-disabled {
			pointer-events: auto; } }

	.swiper-button-prev {
		@include linear-gradient(to right, white 0, rgba(255, 255, 255, 0) 100%);

		&::before {
			@include triangle(left, black, 20px, 1); }

		.no-touchevents &:not(.swiper-button-disabled):hover {
			@include linear-gradient(to right, white 0, rgba(255, 255, 255, 0.5) 80%, rgba(255, 255, 255, 0) 100%); } }

	.swiper-button-next {
		@include linear-gradient(to left, white 0, rgba(255, 255, 255, 0) 100%);

		&::before {
			@include triangle(right, black, 20px, 1); }

		.no-touchevents &:not(.swiper-button-disabled):hover {
			@include linear-gradient(to left, white 0, rgba(255, 255, 255, 0.5) 80%, rgba(255, 255, 255, 0) 100%); } } }

/*tape gallery end */
