@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*MENU REGION */
/*region menu heading */
.region-menu-heading {
	display: block;
	overflow: hidden;
	height: $subsite-menu-tab-height;
	padding: 0 $sidebar-inner-space-horizontal;
	background-color: $subsite-menu-bg-color;
	border-bottom: 1px solid $subsite-menu-border-color;
	color: white;
	cursor: pointer;
	transition-property: background-color;
	transition-duration: $animation-speed;
	@include clear-spacing;

	.disabled-js & {
		cursor: default; }

	body[class*="department-"] .disabled-js & {
		display: none; }

	.no-touchevents .sidebar:not(.disabled-js) &:hover {
		background-color: shade($subsite-menu-bg-color, 70%); }

	&::before {
		@include pseudo($display: null, $pos: relative);
		@include vertical-align($pseudo: null); } }

//.region-menu-heading:first-child + div
//isplay: none

.menu-sub-site {
	background-color: $subsite-menu-bg-color;

	//.popup-nav-small &
	//isplay: none

	//region-page &
 }	//display: block

/*region menu button */
$btn-region-menu-size: 22px;
$btn-region-menu-margin-right: 20px;
.region-menu-btn {
	//<a href="#" class="btn-map-site"><i></i><span>Текс кнопки</span></a>
	$btn-region-menu-bg: white;
	$btn-region-menu-color: white;
	$btn-region-menu-min-height: $header-top-height;
	$btn-region-menu-space: 12px;
	$btn-region-menu-lines-width: 22px;
	$btn-region-menu-lines-height: 3px;
	$btn-region-menu-lines-space: 5px;
	$btn-region-menu-lines-color: $btn-region-menu-color;

	@mixin line-style() {
		@include size($btn-region-menu-lines-width, $btn-region-menu-lines-height);
		background: $btn-region-menu-lines-color; }

	@mixin transition() {
		transition-property: transform, box-shadow, background-color;
		transition-duration: $animation-speed; }

	& {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		@include size($btn-region-menu-size);
		margin-right: 20px;
		text-decoration: none;
		color: $text-color;
		@include clear-spacing;
		white-space: nowrap;

		&::before, &::after {
			@include pseudo();
			@include line-style;
			left: 50%;
			top: 50%;
			@include margin($t: -#{round($btn-region-menu-lines-height*0.5)}, $l: -#{$btn-region-menu-lines-width*0.5});
			opacity: 0;
			transition-property: transform, opacity;
			transition-duration: $animation-speed; }

		.is-expanded & {
			&::before {
				transform: rotate(45deg);
				opacity: 1; }

			&::after {
				transform: rotate(-45deg);
				opacity: 1; } }

		.disabled-js & {
			&::before, &::after {
				transform: rotate(0deg);
				opacity: 0; } }

		> span {
			@include hide-text; }

		> i {
			position: absolute;
			left: 50%;
			top: 50%;
			backface-visibility: hidden;
			@include margin($t: -#{round($btn-region-menu-lines-height/2)}, $l: -#{round($btn-region-menu-lines-width/2)});
			@include line-style;
			transition-property: background-color;
			transition-duration: $animation-speed;

			&::before, &::after {
				@include pseudo();
				left: 0;
				@include line-style;
				backface-visibility: hidden;
				transition-property: transform, opacity;
				transition-duration: $animation-speed; }

			//first line menu
			&::before {
				top: -#{$btn-region-menu-lines-height + $btn-region-menu-lines-space}; }

			//third line menu
			&::after {
				top: #{$btn-region-menu-lines-height + $btn-region-menu-lines-space}; } }

		.is-expanded & > i {
			background-color: transparent;

			&::before {
				transform: translate(0, #{$btn-region-menu-lines-space+$btn-region-menu-lines-height});
				opacity: 0; }

			&::after {
				transform: translate(0, -#{$btn-region-menu-lines-space+$btn-region-menu-lines-height});
				opacity: 0; } }

		.disabled-js & > i {
			background-color: $btn-region-menu-lines-color;

			&::before, &::after {
				transform: translate(0, 0);
				opacity: 1; } } } }

/*region menu button end*/

/*region menu heading text */
.region-menu-heading-text {
	display: inline-block;
	vertical-align: middle;
	@include font(14px, 1.3);
	letter-spacing: normal;
	max-width: calc(100% - #{$btn-region-menu-size} - #{$btn-region-menu-margin-right} - 5px); }

/*region menu heading text end */
/*region menu heading end */

/*sidebar region news */
.sidebar-region-news__heading {
	overflow: hidden;
	height: $subsite-menu-tab-height;
	position: relative;
	padding: 0 round($sidebar-inner-space-horizontal*1.8) 0 $sidebar-inner-space-horizontal;
	background-color: white;
	border-bottom: 1px solid $bg-color;
	cursor: pointer;
	transition-property: background-color;
	transition-duration: $animation-speed;
	@include clear-spacing;

	.no-touchevents &:hover {
		background-color: #f3f3f3; }

	&::before {
		@include pseudo($display: null, $pos: relative);
		@include vertical-align($pseudo: null); }

	&::after {
		@include pseudo();
		top: 50%;
		right: round($sidebar-inner-space-horizontal);
		margin-top: -4px;
		@include triangle(bottom, $text-color, 5px, 1.5);

		.is-expanded & {
			transform: rotate(0.5turn); } }

	h4, h3, h2 {
		margin-bottom: 0;
		@include display-inline-block-middle;
		@include font(20px, 1.3);
		letter-spacing: normal;
		max-width: calc(100% - 5px); } }

.sidebar-region-news__content {
	position: relative;
	background-color: white;
	padding: round($sidebar-inner-space-horizontal*0.8) $sidebar-inner-space-horizontal; }

/*sidebar region news end */

.region-menu-panel {
	position: relative;
	height: calc(100vh - #{$header-height} - #{round($subsite-menu-tab-height*2)});
	@include overflow($x: hidden, $y: auto);

	.no-js & {
		height: auto !important; }

	@include media('<desk-lg') {
		height: calc(100vh - #{$header-height--md} - #{round($subsite-menu-tab-height*2)}); }

	.elements-is-transform & {
		height: calc(100vh - #{$header-top-height} - #{round($subsite-menu-tab-height*2)});

		@include media('<desk-lg') {
			height: calc(100vh - #{$header-top-height--md} - #{round($subsite-menu-tab-height*2)}); } }

	.hasnt-news & {
		height: calc(100vh - #{$header-height} - #{$subsite-menu-tab-height});

		@include media('<desk-lg') {
			height: calc(100vh - #{$header-height--md} - #{$subsite-menu-tab-height}); } }

	body[class*="department-"] .hasnt-news & {
		height: calc(100vh - #{$header-height});

		@include media('<desk-lg') {
			height: calc(100vh - #{$header-height--md}); } }

	.elements-is-transform .hasnt-news & {
		height: calc(100vh - #{$header-top-height} - #{$subsite-menu-tab-height});

		@include media('<desk-lg') {
			height: calc(100vh - #{$header-top-height--md} - #{$subsite-menu-tab-height}); } }

	.elements-is-transform body[class*="department-"] .hasnt-news & {
		height: calc(100vh - #{$header-top-height});

		@include media('<desk-lg') {
			height: calc(100vh - #{$header-top-height--md}); } } }

/*region menu */
// общие стили для главного списка навигации
.region-menu__list {
	position: relative;
	background-color: $subsite-menu-bg-color;
	min-height: 100%; }

// общие стили для подменю навигации
.region-menu__drop {
	position: relative;
	background-color: $subsite-menu-bg-color;
	display: none; }

// общие стили для пунктов навигации
.region-menu__list {

	li {
		position: relative;
		overflow: hidden;

		//&::before, &::after
		//include pseudo()
		//eight: 20px
		//idth: 110%
		//eft: -5%
		//ox-shadow: 0 0 10px rgba(0, 0, 0, 0.25)
		//ransition-property: opacity
		//ransition-duration: $animation-speed
		//-index: 10
		//ointer-events: none
		//pacity: 0

		//&::before
		//op: -20px

		//&::after
		//ottom: -20px

		//&.is-open
		//::before, &::after
 }		//opacity: 1

	.region-menu__tab {
		position: relative; }

	a {
		display: block;
		position: relative;
		color: $subsite-menu-text-color;
		text-decoration: none;
		transition-property: color, background-color;
		transition-duration: $animation-speed;
		padding: 21px $grid-inside-horizontal-space 22px;
		border-bottom: 1px solid $subsite-menu-border-color;
		font-size: 14px;
		line-height: 1.4;
		letter-spacing: 0.02em; } }

// стили для пунктов навигации, у которых ЕСТЬ ПОДМЕНЮ
.region-menu__list li.has-drop > .region-menu__tab > a {
	padding-right: (round($grid-inside-horizontal-space*2.5)); }

// стили для пункта навигации ТЕКУЩЕЙ страницы
.region-menu__list li.current > .region-menu__tab {

	&, .no-touchevents & {

		a, a:hover {
			color: white;
			box-shadow: inset 5px 0 0 white; }

		//& a span
 } }		//order-color: $mark-color-alt

// стили для пункта навигации С ОТКРЫТЫМ ПОДМЕНЮ
.region-menu__list li {
	&.is-open > .region-menu__tab a {
		&::before {
			opacity: 1;
			transform: translate(0, 0); } } }

// особые стили для пунктов навигации ПЕРВОГО урованя
.region-menu__list > li {

	& > .region-menu__tab a {
		font-family: $font-alt;
		@include font(16px, 1.5);
		background-color: $subsite-menu-bg-color;
		@include padding-vertical(18px, 17px);

		.no-touchevents &:hover {
			color: white; } }

	//&.is-open > .region-menu__tab a
	//ackground-color: shade($region-menu-bg-color, 90%)
 }	//order-color: shade($region-menu-border-color, 90%)

// СТРЕЛКИ навигации
.region-menu__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: round($grid-inside-horizontal-space*2);
	height: 100%;
	background-color: $subsite-menu-bg-color;
	border: solid $subsite-menu-border-color;
	border-width: 0 0 1px 1px;
	transition-property: transform, color, background-color;
	transition-duration: $animation-speed;
	z-index: 2;
	cursor: pointer;
	display: none;

	li.has-drop > .region-menu__tab > & {
		display: block; }

	i {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 12px;
		height: 8px;
		pointer-events: none;
		margin: -4px 0 0 -6px;
		transition-property: transform;
		transition-duration: $animation-speed;
		font-size: 0;
		line-height: 0;

		&::before, &::after {
			content: '';
			position: absolute;
			bottom: 0;
			height: 10px;
			width: 2px;
			border-radius: 2px;
			backface-visibility: hidden;
			background-color: $subsite-menu-text-color;
			transform-origin: center center;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		&::before {
			right: 50%;
			transform-origin: right bottom;
			transform: rotate(45deg); }

		&::after {
			left: 50%;
			transform-origin: left bottom;
			transform: rotate(-45deg); } } }

// стили стрелок навигации для разных состояний
.region-menu__angle {

	// активное (подменю открыто)
	li.is-open > .region-menu__tab & i {
		transform: rotate(-0.5turn);

		//&::before, &::after
 }		//ackground-color: $subsite-menu-text-color

	// ховер
	.no-touchevents li > .region-menu__tab &:hover i {
		&::before, &::after {
			background-color: white; } } }

// особые стили для пунктов ПОДМЕНЮ
.region-menu__drop {

	// особые стили для пунктов навигации ВТОРОГО урованя
	ul, a, .region-menu__angle {
		background-color: $subsite-menu-bg-color-s;

		.no-touchevents &:hover {
			color: white; } }

	.region-menu__drop {
		// особые стили для пунктов навигации ТРЕТЬЕГО урованя
		ul, a, .region-menu__angle {
			background-color: $subsite-menu-bg-color-ss; }

		.region-menu__drop {
			// особые стили для пунктов навигации ЧЕТВЕРТОГО урованя
			ul, a, .region-menu__angle {
				background-color: $subsite-menu-bg-color-sss; }

			.region-menu__drop {
				// особые стили для пунктов навигации ПЯТОГО урованя
				ul, a, .region-menu__angle {
					background-color: $subsite-menu-bg-color-ssss; } } } } }

/*MENU REGION end */

/*REGION HEADER*/
.region-header {
	position: relative;
	z-index: 5;
	padding: round($grid-layout-space*0.5) #{$grid-layout-space - 9px}; // -9 для компенсации горизонтального скролла
	min-height: 100px;

	@include media('<desk-hd') {
		padding: round($grid-layout-space--desk-hd*0.3) #{$grid-layout-space--desk-hd - 9px}; } // -9 для компенсации горизонтального скролла

	@include media('<desk-md') {
		padding: round($grid-layout-space--desk-md*0.3) $grid-layout-space--desk-md; }

	@include media('<tab-lg') {
		padding: round($grid-layout-space--desk-md*0.3) $grid-layout-space--desk-md; }

	@include media('<mob-lg') {
		padding: round($grid-layout-space--mob-lg*0.3) $grid-layout-space--mob-lg; }

	&::before {
		@include pseudo();
		@include cover($position: null);
		@include linear-gradient(to right, rgba(0, 0, 0, 0.75) 0, rgba(0, 0, 0, 0.50) 20%, rgba(0, 0, 0, 0) 100%);
		z-index: -1;

		@include media('<mob-lg') {
			background: rgba(0, 0, 0, 0.35); } }

	.h-contacts-opener {
		right: 0;

		i {
			&:after, &:before {
				background-color: #fff; } } } }

.region-header__bg {
	@include cover();
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	z-index: -2; }

.region-header__caption {
	margin-bottom: 0.5em;
	font-family: $font-alt;
	font-weight: 700;
	@include font(2.4rem, 1.3);
	color: white;
	text-shadow: 1px 1px 0 black;
	@include clear;

	@include media('<desk-lg') {
		font-size: 2.2rem; }

	@include media('<tab-lg') {
		min-height: calc(1em*1.3*2);
		font-size: 2.0rem; }

	@include media('<mob-lg') {
		min-height: calc(1em*1.4*2);
		@include font(20px, 1.4); }

	.col {
		float: left;
		width: 46%;
		padding-right: $grid-space;
		text-decoration: none;
		color: inherit;

		@include media('<mob-lg') {
			width: 100%;
			padding: 0;
			margin-bottom: 10px;

			&:last-child {
				margin-bottom: 0; } }

		& > img {
			float: left;
			@include size(32px);
			margin-right: 10px;

			@include media('<desk-lg') {
				@include size(28px); }

			@include media('<tab-lg') {
				@include size(36px); }

			@include media('<mob-lg') {
				@include size(32px); } } }

	h1, h2, h3, .subtitle {
		overflow: hidden;
		font-family: inherit;
		font-weight: inherit;
		@include font(inherit, inherit);
		color: inherit;
		margin: 0; }

	h3, .subtitle-slogan {
		@include font(1.8rem, round-float(24/18, 2));

		@include media('<desk-lg') {
			font-size: 1.4rem; } } }

.region-header__caption, .region-header__holder {
	max-width: 100%; }

.region-header__caption {
	width: 1000px; }

.region-header__holder {
	position: relative;
	width: 850px;

	@include media('<mob-lg') {
		padding-right: 50px; } }

.region-header__holder {
	@include clear;

	.region-logo {
		float: left;
		width: 80px;
		margin-right: 20px;

		@include media('<desk-lg') {
			width: 54px;
			margin-right: 15px; }

		//+media('<mob-lg')
		//loat: none

		@include media('<mob-md') {
			width: 50px; }

		a {
			display: block; } } }

.region-header__info {
	overflow: hidden;
	color: white;
	@include clear-spacing;

	li {
		display: inline-block;
		vertical-align: top;
		padding-right: 20px;
		width: 50%;
		@include font(1.4rem, 1.5);
		letter-spacing: normal;
		//+margin-vertical(round-float($bottom-space*0.4, 1))
		@include margin-vertical(0.6em);
		@include clear;

		@include media('<desk-lg') {
			@include margin-vertical(0.2em); }

		@include media('<mob-lg') {
			width: 100%;
			padding-right: 0;
			@include margin-vertical(0.2em);
			font-size: 14px;
			line-height: 18px;

			display: none;

			.h-contacts-js.is-show & {
				display: inline-block; } }

		&.region-header__info__label {
			display: none;

			@include media('<mob-lg') {
				display: inline-block;
				margin: 0;
				padding: 18px 0;

				.h-contacts-js.is-show & {
					display: none; } } }

		& > span {
			display: block;
			overflow: hidden; } }

	svg {
		float: left;
		width: 24px;
		height: 24px;
		margin-right: $grid-space;

		@include media('<desk-lg') {
			width: 20px;
			height: 20px;
			margin-right: 10px; }

		@include media('<mob-lg') {
			width: 18px;
			height: 18px; } }

	path {
		fill: white; }

	a {
		color: inherit;
		text-decoration: none; } }

/*REGION HEADER end*/
