@function strip-units($number) {
	@return $number / ($number * 0 + 1); }

// =============================================================================
// pow
// =============================================================================
/// Возведение в степень
///
/// @param {Number, Length} $number - Число
/// @param {Number, Length} $exponent - Степень
/// @return {Number} - Значение `$number` возведенно в степень `$exponent`

@function pow($number, $exponent) {
	$value: 1;

	@if $exponent > 0 {
		@for $i from 1 through $exponent {
			$value: $value * $number; } }

	@return $value; }

// =============================================================================
// round-float
// =============================================================================
/// Округление до n знаков после запятой
///
/// @param {Number, Length} $number - Число
/// @param {Number, Length} $n - Количество знаков после запяной
/// @return {Number} - Значение округленное до `n` знаков после запятой

@function round-float($number, $n) {
	//$unit: unit($number)
	//$number: strip-units($number)
	//@debug $unit
	//@debug $number
	$float: pow(10,$n);

	$value: round($number*$float)/$float;

	//$valueWithUnit: $value + $unit
	//@debug $valueWithUnit
	@return $value; }

// Примесь используется только в текущем для этого проекта
// Устанавливает заначения параметров анимации `transition`
// для превью новостей в гридах
@mixin news-transition() {
	transition-property: transform, color, fill;
	transition-duration: $animation-speed; }
