@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

///*=================*/
/*section*/
///*=================*/
.section {
	margin-bottom: $bottom-space-md;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; }

	&:last-child {
		margin-bottom: 0; }

	* ~ & {
		margin-top: $bottom-space-md;

		@include media('<desk-lg') {
			margin-top: $bottom-space; }

		@include media('<mob-lg') {
			margin-bottom: 2rem; } }

	script ~ &, input ~ & {
		margin-top: 0; }

	& > *:last-child {
		margin-bottom: 0; } }

.section__title {
	position: relative;
	font-family: $font-alt;
	margin-bottom: $bottom-space-sm;
	color: $text-color;
	font-size: 2rem;
	line-height: 1.3;

	.home-page & {
		color: $text-color-alt; }

	h1, h2, h3, & > a {
		@include display-inline-block-middle;
		position: relative;
		font-family: inherit;
		color: inherit;
		@include font(inherit, inherit);
		margin: 0;

		@include media('<mob-lg') {
			display: block;
			margin-bottom: (round-float($bottom-space-sm*0.5, 1)); }

		& > a {
			font-weight: inherit; } }

	&.has-date {
		h1, h2, h3 {
			@include media('<mob-lg') {
				display: inline-block;
				margin-right: 4px;
				margin-bottom: 0; } } }

	h1, h2, h3 {
		vertical-align: top; }

	& > a {
		font-size: 0.9em;
		font-weight: normal;
		top: 0.1em;
		cursor: pointer;

		span {
			border-bottom: 1px solid transparent;
			transition-property: border-color;
			transition-duration: $animation-speed; }

		.no-touchevents a:hover {
			text-decoration: none; }

		&.type-anchor {
			text-decoration: none;

			span {
				border-bottom: 1px dotted $text-color; }

			.no-touchevents &:hover span {
				border-bottom-color: transparent; } } }

	.v-separator {
		font-size: 0.6em;
		vertical-align: middle;

		& + a {
			color: $text-color;
			text-transform: uppercase;
			@include font(0.6em, 1);
			vertical-align: middle; } }

	.touchevents & .news-date {
		display: inline-block;
		vertical-align: middle;
		line-height: 1;

		& ~ a {
			margin-bottom: 0; } } }

.v-separator {
	@include display-inline-block-middle;
	@include margin-horizontal(10px);

	@include media('<mob-lg') {
		display: none;

		.news-date + & {
			display: inline-block; } }

	.section__title & {
		color: $text-color; }

	& + a {
		@include display-inline-block-middle;
		top: auto; } }

.section__box {
	background-color: white;
	padding: $grid-inside-horizontal-space;

	@include media('<mob-lg') {
		padding: $grid-layout-space--mob-lg; } }

.datepicker-input {
	position: relative;
	top: 0.1em;
	@include display-inline-block-middle;
	padding: 0;
	margin: 0;
	font-family: $font-alt;
	@include font(0.9em, 23px);
	height: 23px;
	font-weight: normal;
	cursor: pointer;
	border: 1px solid $text-color;
	background: white;

	@include media('>=desk-lg') {
		vertical-align: top;
		height: 20px;
		line-height: 20px; }

	//&::-webkit-inner-spin-button, &::-webkit-calendar-picker-indicator
	//webkit-appearance: none
	//isplay: none

	.no-touchevents .datepicker-alt & {
			position: absolute;
			left: 0;
			bottom: 0;
			top: auto;
			width: 100%;
			height: 0;
			opacity: 0;
			padding: 0;
			border: 0;
			margin: 0 0 -5px; } }

.section__subtitle {
	margin-bottom: $bottom-space-sm;
	font-family: $font-alt;
	font-weight: normal;
	@include font(1.2rem, 1.6);
	text-transform: uppercase;
	color: $text-color;

	h1, h2, h3, h4 {
		@include display-inline-block-middle;
		font-weight: inherit;
		font-family: inherit;
		@include font(inherit, inherit);
		color: inherit;
		margin-bottom: 0;

		@include media('<mob-lg') {
			font-size: 1.4rem;
			display: block;
			margin-bottom: ($bottom-space-sm); } }

	a {
		text-decoration: underline;
		color: inherit;

		.no-touchevents a:hover {
			text-decoration: none; } } }

.hide-tab-lg {
	display: none;

	@include media('<desk-lg') {
		display: block; } }

.filter-item {
	max-width: 320px; }

///*=================*/
/*section end*/
///*=================*/

///*=================*/
/*datepicker*/
///*=================*/
.datepicker-overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	z-index: 99998;
	opacity: 0;
	visibility: hidden;
	transition-property: opacity, visibility;
	transition-duration: $animation-speed;

	.datepicker-overlay-is-visible & {
		opacity: 1;
		visibility: visible; } }

///*=================*/
/*datepicker end*/
///*=================*/

///*=================*/
/*banners*/
///*=================*/
.banners {
	@include grid-parent($grid-space);
	@include clear-spacing;
	margin-bottom: $bottom-space;

	.section & {
		margin-bottom: 0; } }

.banners__item {
	@include font($font-size, $line-height);
	letter-spacing: normal;

	.banners & {
		@include grid-item(1, $grid-space);

		&:last-child {
			margin-bottom: 0; } }

	.banners-panel & {
		@include grid-item(4, $grid-space);

		//+media('<desk-lg')
		//include grid-item(3, $grid-space)

		@include media('<tab-lg') {
			@include grid-item(2, $grid-space); } }

	a {
		display: block;
		color: $text-color;
		text-decoration: none; }

	img {
		@include push-auto;
		width: 100%;

		&[src*="preloader.svg"] {
			width: 48px; } } }

///*=================*/
/*banners end*/
///*=================*/

///*=================*/
/*context banners */
///*=================*/
.context-banner {
	display: block;
	background-position: center top;
	background-repeat: no-repeat;
	background-size: cover;
	text-decoration: none;
	color: $text-color;

	&::before {
		content: "";
		display: block;
		@include ratio(16, 9); } }

///*=================*/
/*context banners end */
///*=================*/

///*=================*/
/*infoblock*/
///*=================*/
.infoblock {
	margin-bottom: $bottom-space;
	overflow: hidden;

	&:last-child {
		margin-bottom: 0; } }

.infoblock__list {
	// перед загрузкой слайды нужно выставить в один ряд, чтобы не прыгал контент
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end
	@include margin-horizontal(-#{round($grid-space/2)});
	margin-bottom: 0;

	&.slick-initialized, .no-js & {
		// после загрузки вернуть стили по дефолту
		// after slide load
		white-space: normal;
		overflow: visible;
 }		// after slide load end

	&.slick-dotted {
		margin-bottom: 0; } } // обнуляем отступ снизу

.infoblock__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end
	@include padding-horizontal(round($grid-space/2));

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 }		// after slide load end

	a {
		display: block; }

	img {
		display: block;

		&[src*="preloader.svg"] {
			width: 48px; } } }

.infoblock__list {
	.slick-arrow {
		width: 30px;
		height: 30px;
		background-color: $text-color-alt; } }

///*=================*/
/*infoblock end*/
///*=================*/

///*=================*/
/*news links*/
///*=================*/
.news-links {
	@include grid-parent($grid-space);
	@include clear-spacing;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	.popup-news & + a {
		margin: 0 0 25px 25px; } }

.news-links__item {
	@include grid-item(1, $grid-space, $bottom-space);
	@include font($font-size-small, $line-height-small);
	letter-spacing: normal;

	&:last-child {
		margin-bottom: 0; } }

.news-links__inner {
	display: block;
	position: relative;
	text-decoration: none;
	color: $text-color;
	z-index: 1; }

.news-links__text {
	margin-bottom: 12px;
	transition-property: background-color, color;
	transition-duration: $animation-speed-mouseleave; }

.no-touchevents .news-links__inner:hover .news-links__text {
	color: $text-color-alt; }

.news-links__meta {
	display: block;
	@include font(12px, 1.4);
	color: $text-color-light;
	transition-property: background-color, color;
	transition-duration: $animation-speed-mouseleave; }

///*=================*/
/*news links end*/
///*=================*/

///*=================*/
/*news read*/
///*=================*/
.news-read {
	@include grid-parent($grid-space);
	@include clear-spacing;
	margin-bottom: $bottom-space; }

.news-read__item {
	@include grid-item(1, $grid-space, $bottom-space);
	@include font($font-size-small, $line-height-small);
	letter-spacing: normal;

	&:last-child {
		margin-bottom: 0; } }

.news-read__inner {
	display: block;
	position: relative;
	text-decoration: none;
	color: $text-color;

	&::before {
		@include pseudo();
		$left: 18px;
		$top: 12px;
		left: -#{$left};
		top: -#{$top};
		width: calc(100% + #{$left}*2);
		height: calc(100% + #{$top}*2);
		transition-property: background-color, color;
		transition-duration: $animation-speed;
		z-index: -1; }

	.no-touchevents &:hover::before {
		background-color: #fff; } }

.news-read__text {
	margin-bottom: 12px; }

.news-read__meta {
	display: block;
	@include font(1.2rem, 1.4);
	color: $text-color-light; }

///*=================*/
/*news read end*/
///*=================*/

///*=================*/
/*SIDE*/
///*=================*/
.side {
	position: absolute;
	margin-top: $grid-layout-space*0.8 - 9px;
	margin-left: -#{$side-width};
	left: 100%;
	width: $side-width;
	z-index: 50;

	@include media('<desk-hd') {
		margin-top: round($grid-layout-space--desk-hd*0.5); }

	@include media('<desk-md') {
		display: none; }

	&.is_stuck {
		left: $main-layout-width;

		&.is_bottom[style*="absolute"] {
			//left: $main-layout-width - $sidebar-left-width
			left: auto;
			right: 0;
			margin-right: 0 !important; }

		@include media('<desk-hd') {
			.no-touchevents & {
				left: 100%; } }

		@include media('<desk-hd') {
			.touchevents & {
				left: 100%; }

			.touchevents &.is_bottom {
				margin-left: 0; } }

		& + div {
			z-index: -1; } }

	@include media('<desk-lg') {
		margin-right: -#{$grid-layout-space--desk-hd + $side-width}; } }

.side__list {
	width: $side-width;
	position: relative;
	z-index: 10; }

.side__item {
	position: relative;

	&::before {
		content: "";
		display: block;
		padding-top: calc(100% + 1px); } }

.side__link {
	position: absolute;
	top: 0;
	left: 0;
	@include size(100%);
	color: $text-color;
	white-space: nowrap;
	text-decoration: none; }

.side__inner {
	background: none;
	position: absolute;
	top: 0;
	left: 100%;
	height: 100%;
	@include clear-spacing;
	white-space: nowrap;
	pointer-events: none;
	background: white;
	@include antialiased;
	transform: translate(-#{$side-width}, 0);
	transition-property: background-color, transform, box-shadow, border-color, opacity;
	transition-duration: $animation-speed-mouseleave;
	transition-timing-function: $side-transition-timing-function;

	a:hover & {
		transform: translate(-100%, 0);
		transition-duration: $animation-speed;
		pointer-events: auto; } }

.side__depict {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	padding-right: {}
	height: 100%;
	width: $side-width;
	background-color: $text-color-alt;
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
	transition-property: background-color, transform, box-shadow, border-color, opacity;
	transition-duration: $animation-speed-mouseleave;
	transition-timing-function: $side-transition-timing-function;

	a:hover & {
		border-color: transparent;
		transition-duration: $animation-speed; }

	svg {
		width: 30px;
		height: 30px;
		position: absolute;
		left: 50%;
		top: 50%;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
		transform: translate(-50%, -50%); }

	path {
		fill: #fff; } }

.side__text {
	display: inline-block;
	vertical-align: middle;
	padding: 0 20px;
	max-height: 100%;
	font: 400 #{$side-font-size}/#{$side-line-height} $font-alt;
	letter-spacing: normal;

	span {
		display: block;
		overflow: hidden;
		max-height: $side-line-height*2;
		opacity: 0;
		transform: translate(20px, 0);
		transition-property: background-color, transform, box-shadow, border-color, opacity;
		transition-duration: $animation-speed-mouseleave;
		transition-timing-function: $side-transition-timing-function;

		a:hover & {
			transform: translate(0, 0);
			opacity: 1;
			transition-duration: 0.5s; } } }

///*=================*/
/*SIDE end*/
///*=================*/

///*=================*/
/*tags*/
///*=================*/
.tags {
	.article-layout & {
		border-top: 1px solid $border-color;

		&:last-child {
			margin-bottom: -#{$article-layout-space-vertical};

			@include media('<desk-hd') {
				margin-bottom: -#{$article-layout-space-vertical--desk-hd }; }

			@include media('<tab-lg') {
				margin-bottom: -#{$article-layout-space-vertical--tab-lg}; }

			@include media('<mob-lg') {
				margin-bottom: -#{$article-layout-space-vertical--mob-lg}; } } }

	h3 {
		margin-bottom: $bottom-space; } }

.tags-wrap {
	overflow: hidden; }

.tags__list, .#{$user-content} .tags__list {
	font-size: 0;
	//letter-spacing: -4px
	margin: 0 #{-(round($grid-space*0.5))};
	text-align: left;

	.tags-wrap &:last-child {
		margin-bottom: -#{$grid-space}; }

	.article-layout & {
		max-width: none; }

	li {
		display: inline-block;
		vertical-align: top;
		white-space: nowrap;
		font-family: $font-alt;
		font-weight: 400;
		font-size: 1.4rem;
		line-height: 2.1;
		letter-spacing: normal;
		text-transform: uppercase;
		margin-top: 0;
		margin-bottom: $grid-space;
		padding: 0 round($grid-space*0.5);

		&::before {
			content: none; }

		a {
			position: relative;
			display: inline-block;
			vertical-align: top;
			overflow: hidden;
			padding: 0 16px;
			color: $text-color;
			text-decoration: none;
			text-transform: uppercase;
			border: solid $mark-color-alt;
			border-width: 1px 1px 4px;
			background: white;
			//transition-property: transform
			//transition-duration: $animation-speed-mouseleave
			@include antialiased;

			&::before {
				@include pseudo('#', inline-block, relative);
				z-index: 5; }

			&::after {
				@include pseudo();
				@include cover($position: null, $height: 102%);
				background-color: $mark-color-alt;
				transform: translate(0, 100%);
				transition-property: transform;
				transition-duration: $animation-speed-mouseleave; }

			.no-touchevents &:hover::after {
				transform: translate(0); }

			&:active {
				transform: scale(0.95); }

			& > span {
				display: inline-block;
				position: relative;
				z-index: 5; } } } }

.tags .tags__list {
	margin-bottom: -($bottom-space/2);
	text-align: left; }

///*=================*/
/*tags end*/
///*=================*/

///*=================*/
/*years*/
///*=================*/
.years {
	@include display-inline-block-top;
	position: relative;
	top: 2px;
	perspective: 1500px;
	//width: 82px
	//height: 48px
	z-index: 10;

	a {
		display: block;
		position: relative;
		line-height: 24px;
		color: $text-color;
		text-align: center;
		text-decoration: none; }

	& > a {
		height: 100%;
		font-family: inherit;
		font-weight: 700;
		transition-property: background, border;
		transition-duration: $animation-speed;
		color: $text-color-alt;
		z-index: 40;

		@include clear-spacing;

		//&::before
		//ontent: ''
		//isplay: inline-block
		//eight: 100%
		//ertical-align: middle

		& > span, & > i {
			@include display-inline-block-middle;
			position: relative;
			font-family: inherit;
			font-style: normal;
			font-weight: inherit;
			line-height: 1;
			transition-property: border;
			transition-duration: $animation-speed;
			z-index: 1;

			color: inherit;
			font-size: 2rem;
			letter-spacing: normal; }

		& > span {
			border-bottom: 1px dotted $text-color-alt;
			margin-right: 6px; }

		.no-touchevents &:hover {

			& > span {
				border-color: transparent; } } } }

.years-drop, .#{$user-content} .years-drop {
	padding: 10px 0;
	margin: 0 0 0 -4px;

	li {
		padding: 0;
		margin: 0;

		&::before {
			content: none; } } }

.years-drop {
	position: absolute;
	top: 100%;
	left: -10px;
	width: calc(100% + 20px);
	@include padding-horizontal(10px);
	text-align: center;
	background: white;
	box-shadow: 0 15px 25px rgba(0, 0, 0, 0.15);
	box-sizing: border-box;
	opacity: 0;
	visibility: hidden;
	transform-origin: center top;
	transform: rotateX(-30deg);
	transition-property: opacity, visibility, transform;
	transition-duration: $animation-speed;
	transition-timing-function: cubic-bezier(0.25, 1, 0.25, 1);
	z-index: 35;

	&::before {
		@include pseudo();
		left: 0;
		bottom: 100%;
		height: 34px;
		width: 100%;
		background: white;
		z-index: -1; }

	.choice-opened & {
		opacity: 1;
		visibility: visible;
		-webkit-transform: rotateX(0deg);
		transform: rotateX(0deg); }

	a {
		display: block;
		padding: 5px 10px;
		text-align: center;
		position: relative;
		transition-property: color;
		transition-duration: $animation-speed;
		top: auto;

		.no-touchevents &:hover, &.active {
			color: $text-color-alt; }

		& > span {
			border-bottom: none; } } }

///*=================*/
/*years end*/
///*=================*/

///*=================*/
/*sorting */
///*=================*/
.sorting-options {
	@include font($font-size-small, $line-height-small);
	font-weight: normal;
	color: $text-color-light;

	.popup-title & {
		padding-top: 13px; } }

.sorting-select {
	//perspective: 1500px
	position: relative;
	z-index: 10;

	a {
		display: block;
		position: relative;
		color: $text-color;
		text-decoration: none; }

	& > a {
		z-index: 10;

		& > span {
			display: inline-block;
			position: relative;
			border-bottom: 1px dotted $text-color;
			z-index: 1; } }

	.sorting-label + & {
		@include display-inline-block-top; } }

.sorting-label {
	@include display-inline-block-top;
	margin-right: 6px; }

.sorting-select-drop {
	& {
		width: calc(100% + 40px); }

	&, .#{$user-content} & {
		padding: 10px 0;
		margin: 0 0 0 -20px;

		li {
			padding: 0;
			margin: 0;

			&::before {
				content: none; } } } }

.sorting-select-drop {
	position: absolute;
	top: 100%;
	left: 0;
	min-width: 100%;
	background: white;
	box-shadow: 0 15px 25px rgba(0, 0, 0, 0.15);
	box-sizing: border-box;
	opacity: 0;
	visibility: hidden;
	white-space: nowrap;
	transform-origin: center top;
	transform: rotateX(-30deg);
	transition-property: opacity, visibility, transform;
	transition-duration: $animation-speed;
	transition-timing-function: cubic-bezier(0.25, 1, 0.25, 1);
	z-index: 5;

	.choice-opened & {
		opacity: 1;
		visibility: visible;
		-webkit-transform: rotateX(0deg);
		transform: rotateX(0deg); }

	a {
		white-space: normal;
		display: block;
		padding: 5px 20px;
		position: relative;
		transition-property: color;
		transition-duration: $animation-speed;

		.no-touchevents &:hover, &.active {
			color: $text-color-alt; } } }


.news-date, .result-date {
	position: relative;
	font-size: inherit;
	@include display-inline-block-top;
	border-color: $text-color;

	.no-js & {
		display: none; }

	.sorting-options & {
		font-size: 14px;
		border-color: $text-color-light; }

	a {
		@include display-inline-block-top;
		text-decoration: none;
		color: inherit;
		cursor: pointer;
		top: auto;
		border-color: inherit;

		& > span {
			//+display-inline-block-middle
			display: inline-block;
			line-height: 1;
			position: relative;
			border-bottom: 1px dotted;
			border-color: inherit;
			z-index: 1; } }

	.touchevents & > a {
		display: none; }

	.touchevents & input[type="text"] {
		margin-bottom: 0;
		padding: 8px 6px;
		height: 42px;
		width: 120px;
		text-align: center;
		font-size: 14px; } }

.home-page {
	.section__title, .section__subtitle {
		.news-date, .result-date {
			font-weight: bold;
			color: $text-color-alt;
			border-color: $text-color-alt; } } }

///*=================*/
/*sorting end*/
///*=================*/

///*=================*/
/*save info*/
///*=================*/
.save {
	display: block;
	position: relative;
	//margin-bottom: $grid-space
	height: $news-item-height; // Хардкод. Высота должна быть равно высоте элемета новостей
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	text-decoration: none;
	overflow: hidden;

	@include media('<desk-hd') {
		height: $news-item-lg-img-height - $events-summary-nav-height - $grid-space; } // Хардкод. Высота должна равняться высоте изображения (или слайдера) большой новости минус высота переключателей табов блока ниже

	@include media('<desk-lg') {
		height: ($news-item-lg-height - $news-item-lg-img-height + $events-summary-nav-height); } // Хардкод. Высота должна равняться высоте большой новости минус

	@include media('<mob-lg') {
		height: auto;
		@include padding-vertical(10px); }

	//&::before
	//include pseudo($pos: relative)
	//include ratio(1, 1)

	&::after {
		@include pseudo();
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: $text-color-alt;
		opacity: 0.65; } }

.save__header {
	font-size: 14px;
	text-align: center;
	padding: 1rem;
	font-weight: 700;
	color: #fff;
	line-height: 1.1;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 5;

	@include media('<mob-lg') {
		display: block;
		position: relative;
		top: auto;
		left: auto;
		width: auto;
		padding-bottom: 0; } }

.save__sep {
	display: none; }

.save__holder {
	position: absolute;
	left: 0;
	top: 4rem;
	width: 100%;
	height: calc(100% - 4rem);
	z-index: 1;
	@include padding-horizontal($grid-inside-horizontal-space);

	@include media('<mob-lg') {
		position: relative;
		left: auto;
		top: auto;
		height: auto; } }

.save__box {
	height: 50%;
	position: relative;
	border-top: 1px solid #fff;

	&:last-child {
		height: 100%; }

	& ~ .save__box {
		height: 50%; }

	@include media('<mob-lg') {
		height: auto;
		@include padding-vertical(10px); } }

.save__box:first-child {
	border-top: none; }

.save__align {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 100%;
	color: white;
	text-align: center;
	transform: translate(-50%, -50%);
	@include antialiased;
	z-index: 5;

	@include media('<mob-lg') {
		position: relative;
		left: auto;
		top: auto;
		transform: none; }

	& > strong, & > span {
		display: block;
		font-family: $font-alt;
		font-weight: 700; }

	& > strong {
		font-size: 3.6rem;
		line-height: 1.1; }

	& > span {
		font-size: 1.8rem;
		line-height: 1.3; } }

///*=================*/
/*save info end*/
///*=================*/

///*=================*/
/*events summary*/
///*=================*/
.events-sum {
	position: relative;
	margin-bottom: $bottom-space;
	height: $news-item-height; // Хардкод. Высота должна равняться блоку новостей с картинкой
	overflow: hidden;
	background-color: white;
	opacity: 0;

	&.js-tabs-initialized, .no-js & {
		opacity: 1; }

	@include media('<desk-hd') {
		height: ($news-item-lg-height - $news-item-lg-img-height + $events-summary-nav-height); } // Хардкод. Высота должна равняться высоте большой новости минус

	.no-js & {
		height: auto; }

	&:last-child {
		margin-bottom: 0; } }

.events-sum__nav {
	height: $events-summary-nav-height;

	.no-js & {
		display: none; } }

.events-sum__tab {
	height: 100%;

	$min: 1 !default;
	$max: 3 !default;

	@for $item from $min through $max {

		$cur-item: $item - 1;

		$percentage: 100/($max - 1)*$cur-item*1%;

		&:nth-child(#{$item}) a {
			background-color: mix($mark-color-alt, $mark-color, $percentage); } }

	//.no-touchevents &:hover
	//ackground-color: mix(shade($mark-color-alt, 90%), shade($mark-color, 90%), $percentage)

	a {
		display: block;
		position: relative;
		overflow: hidden; // для обрезания стрелки активного таба
		height: 100%;
		text-decoration: none;
		color: $text-color;
		transition-property: background-color;
		transition-duration: $animation-speed;

		&::after {
			@include pseudo();
			@include triangle(top, white, 8px, 1.2);
			top: 100%;
			left: 50%;
			margin-left: -8px;
			will-change: transform;
			transition-property: transform;
			transition-duration: $animation-speed; }

		//.no-touchevents &:hover
		//pacity: 0.8

		&.active-tab {
			&::after {
				transform: translate(0, -100%); } }

		& > span {
			@include hide-text;
			@include antialiased; }

		svg {
			display: block;
			position: absolute;
			left: 50%;
			top: 50%;
			width: 30px;
			height: 30px;
			margin: -15px 0 0 -15px;
			transform: scale(0.8);
			transition-property: transform;
			transition-duration: $animation-speed;

			path, polygon {
				fill: white; } }

		.no-touchevents &:hover svg {
			transform: scale(1); } } }

.events-sum__panels {
	height: calc(100% - #{$events-summary-nav-height}); // высота блока минус высота переключателей табов

	white-space: nowrap;
	overflow: hidden;

	.js-tabs-initialized &, .no-js & {
		white-space: normal; }

	.no-js & {
		height: auto; } }

.events-sum__panel {
	$events-sum-bottom: 50px; // нижний отступ больше, чем верхний

	display: inline-block;
	vertical-align: top;
	position: relative;
	top: 0;
	background-color: white;
	padding: 10px $grid-inside-horizontal-space $events-sum-bottom;
	height: 100%;
	overflow: hidden;
	white-space: normal;

	.js-tabs-initialized &, .no-js & {
		display: block; }

	&::after {
		@include pseudo();
		bottom: 0;
		left: 0;
		width: 100%;
		height: $events-sum-bottom;
		@include linear-gradient(to top, white 0, white 80%, rgba(255, 255, 255, 0) 100%);
		z-index: 5; } }

.events-sum__date {
	display: block;
	font-size: 1.2rem;
	line-height: 1.2;
	color: #999; }

.events-list {
	position: relative; }

.events-list__item {
	border-bottom: 1px solid $border-color;
	@include font(1.4rem, 1.43);
	@include padding-vertical(round($grid-inside-vertical-space*0.4));

	&:last-child {
		border-color: transparent; }

	h3 {
		font-size: 1.8rem;
		line-height: 1.2;
		margin-bottom: 8px;

		&:last-child {
			margin-bottom: 0; } }

	p {
		margin-bottom: 0; }

	img {
		display: block;
		margin: 0 auto; } }

.events-sum__footer {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	padding: 14px $grid-inside-horizontal-space 20px;
	z-index: 10;
	@include truncate(100%);
	line-height: 1; }

.events-warn {
	$size-warn: 76px;
	$size-warn--mob: 40px;
	//============
	position: absolute;
	left: 50%;
	top: 50%;
	@include size($size-warn);
	margin: -#{round($size-warn*0.8)} 0 0 -#{round($size-warn/2)};
	color: #ff562f;
	opacity: 0.25;
	z-index: -1;

	@include media('<mob-lg') {
		@include size($size-warn--mob);
		margin: -$size-warn--mob 0 0 -#{round($size-warn--mob/2)}; }

	svg {
		display: inline-block;
		vertical-align: top;
		@include size(100%); }

	path {
		fill: currentColor; } }

///*=================*/
/*events summary end*/
///*=================*/

///*=================*/
/*obzh*/
///*=================*/
.obzh {
	position: relative;
	margin-bottom: $bottom-space;
	background-color: white;

	&:last-child {
		margin-bottom: 0; } }

.obzh__nav {
	display: table;
	table-layout: fixed;
	width: 100%;

	@include media('<desk-hd') {
		display: block;
		width: 160px;
		float: left; }

	@include media('<tab-lg') {
		display: none; }

	.no-js & {
		display: none; } }

.obzh__links {
	display: none;
	column-count: 2;
	column-gap: 0;
	@include clear;

	@include media('<tab-lg') {
		display: block; }

	@include media('<mob-lg') {
		column-count: auto;
		column-gap: 0; } }

.obzh__tab {
	display: table-cell;
	vertical-align: top;

	$minGroup1: 1 !default;
	$maxGroup1: 3 !default;

	@for $item from $minGroup1 through $maxGroup1 {

		$cur-item: $item - 1;

		$percentage: 100/($maxGroup1 - 1)*$cur-item*1%;

		&:nth-child(#{$item}) a {
			background-color: mix($mark-color-alt, $mark-color, $percentage); } }

	//.no-touchevents &:hover
	//ackground-color: mix(shade($mark-color-alt, 90%), shade($mark-color, 90%), $percentage)

	$minGroup2: 4 !default;
	$maxGroup2: 6 !default;

	@for $item from $minGroup2 through $maxGroup2 {

		$cur-item: $item - 1;

		$percentage: 100/($maxGroup2 - 1)*$cur-item*1%;

		&:nth-child(#{$item}) a {
			background-color: mix(#004a80, #0982db, $percentage); } }

	//.no-touchevents &:hover
	//ackground-color: mix(shade(#004a80, 95%), shade(#0982db, 95%), $percentage)

	.obzh__links & {
		display: inline-block;
		vertical-align: top;
		width: 100%;

		@include media('<mob-lg') {
			display: block; } }

	@include media('<desk-hd') {
		display: block; }

	a {
		display: table;
		width: 100%;
		position: relative;
		height: $obzh-tabs-nav-height;
		text-decoration: none;
		color: $text-color;
		transition-property: background-color;
		transition-duration: $animation-speed;

		& > span {
			display: table-cell;
			font-family: $font-alt;
			font-weight: 700;
			@include font(18px, 20px);
			color: white;
			vertical-align: middle;
			position: relative;
			@include antialiased;
			@include padding-horizontal($grid-inside-horizontal-space);
			transition-property: padding;
			transition-duration: $animation-speed;

			&::after {
				@include pseudo();
				@include triangle(top, white, 8px, 1.2);
				bottom: 0;
				left: 50%;
				margin-left: -8px;
				opacity: 0;
				pointer-events: none;
				transition-property: opacity;
				transition-duration: $animation-speed;

				.obzh__links & {
					display: none !important;
					content: none !important; } }

			@include media('<desk-hd') {
				@include padding-horizontal(20px);
				@include font(16px, 20px);

				&::after {
					@include triangle(left, white, 8px, 1.2);
					bottom: auto;
					left: auto;
					top: 50%;
					right: 0;
					margin-left: 0;
					margin-top: -8px; } } }

		&.active-tab > span::after {
			opacity: 1; } } }

.obzh__panels {
	height: #{$obzh-tabs-nav-height*6}; // количество переключателей на их высоту

	@include media('<tab-lg') {
		display: none !important; }

	.no-js & {
		height: auto !important; } }

.obzh__panel {
	display: block;
	position: relative;
	background-color: white;
	padding: 20px;
	height: 100%;
	overflow: hidden;

	.no-js & {
		padding: 0; } }

.obzh-banner {
	position: relative;
	height: 100%;
	//min-height: 280px // Хардкод. Необязательный ораничитель минимальной высоты
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;

	&[style*="preloader.svg"] {
		background-size: 48px; } }

.obzh-banner__content {
	@include cover();
	padding: 40px 45px;
	display: flex;
	flex-flow: column nowrap;
	justify-content: space-between;
	@include linear-gradient(to right, rgba(255, 255, 255, 0.80) 0, rgba(255, 255, 255, 0) 100%);
	@include font($font-size-small, $line-height-small);
	font-weight: 400;

	@include media('<desk-hd') {
		padding: round(30px*0.85) 30px; }

	.no-js & {
		position: relative;
		display: block;
		left: auto;
		top: auto; } }

.obzh-banner__text {
	overflow: hidden;
	margin-bottom: 1em;
	max-height: 176px; // Магическое число. Примерно обрезает контент для браузеров не поддерживающих `flex: box`
	width: 480px;
	max-width: 80%;

	.no-js & {
		max-width: none;
		max-height: none; }

	ul, ol {
		li {
			margin: 0 0 1em;

			&:last-child {
				margin-bottom: 0; } } }

	ul li {
		&::before {
			top: calc(1em * #{$line-height-small} / 2); } }

	ul, ol, p, h2, h3, h4, h5 {
		@include margin-vertical(1em); }

	& > *:first-child {
		margin-top: 0; }

	& > *:last-child {
		margin-bottom: 0; } }

///*=================*/
/*obzh end*/
///*=================*/

///*=================*/
/*lead */
///*=================*/
.lead {
	position: relative;
	background-color: white;
	overflow: hidden;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.lead__description {
	padding: $grid-inside-vertical-space $grid-inside-horizontal-space;
	border-bottom: 1px solid $border-color;
	overflow: hidden;

	p:last-child {
		margin-bottom: 0; } }

.lead__list {
	margin-left: -1px;
	margin-bottom: -1px;
	margin-right: -1px;
	@include clear; }

.lead__item {
	position: relative;
	float: left;
	width: 33.33%;
	padding: 0 0 1px 1px;
	text-align: center;

	@include media('<mob-lg') {
		width: 100%; } }

.lead__layer, .lead__layer--front, .lead__layer--back {
	position: relative;
	padding: $grid-inside-vertical-space $grid-inside-horizontal-space 80px;
	background: white;
	box-shadow: -1px 0 0 $border-color, 1px 0 0 $border-color, 0 1px 0 $border-color, 0 -1px 0 $border-color;
	min-height: 100%;

	transition-property: opacity, visibiltiy;
	transition-duration: $animation-speed; }

.lead__layer--front {

	.fb-switcher-active & {
		opacity: 0;
		visibility: hidden; } }

.lead__layer--back {
	position: absolute;
	left: 1px;
	top: 0;
	right: 0;
	bottom: 1px;
	background-color: #fff;
	min-height: calc(100% - 1px);

	opacity: 0;
	visibility: hidden;

	.no-js & {
		opacity: 1;
		visibility: visible;
		position: relative;
		left: auto;
		top: auto;
		right: auto;
		bottom: auto; }

	.fb-switcher-active & {
		opacity: 1;
		visibility: visible; } }

.lead__position {
	@include font(1.6rem, 1.2);
	color: $text-color-light;
	margin-bottom: $bottom-space-sm; }

.lead__name {
	font-family: $font-alt;
	font-weight: bold;
	@include font(1.8rem, 1.3);
	margin-bottom: $bottom-space-sm; }

.lead__footer {
	position: absolute;
	left: 0;
	width: 100%;
	bottom: $grid-inside-vertical-space;

	.no-js & {
		display: none; }

	a {
		color: $text-color-alt;
		text-decoration: none;

		span {
			border-bottom: 1px dotted $text-color-alt;
			transition-property: border-color;
			transition-duration: $animation-speed; }

		.no-touchevents &:hover span {
			border-bottom-color: transparent; } } }

.lead__schedule {
	text-align: left;
	@include antialiased;

	dl {
		padding: 0;
		margin: 0 0 $bottom-space-sm;

		&:last-child {
			margin-bottom: 0; }

		dt, dd {
			margin: 0;
			padding: 0; }

		dt {
			float: left;
			margin-right: 6px;
			@include font-bold; } } }

///*=================*/
/*lead end */
///*=================*/

///*=================*/
/*history info */
///*=================*/
$history-info-inside-space: 80px;

.history-info {
	@include clear; }

.history-info__holder {
	background-color: white;
	padding: $history-info-inside-space;

	@include media('<desk-hd') {
		padding: $article-layout-space-horizontal--desk-hd; }

	@include media('<tab-lg') {
		padding: $article-layout-space-horizontal--tab-lg; }

	@include media('<mob-lg') {
		padding: $article-layout-space-horizontal--mob-lg; } }

.history-info__img {
	float: right;
	border: 10px solid $bg-color;
	margin-left: $history-info-inside-space;

	@include media('<desk-lg') {
		border-width: 6px;
		margin-left: $article-layout-space-horizontal--tab-lg;
