	@include media('<mob-md') {
		display: block;
		float: none;
		@include push-auto;
		width: 240px;
		max-width: 100%;
		margin-bottom: $bottom-space; } }

.history-info__text {
	overflow: hidden;
	@include font(1.6rem, 1.875);
	margin-bottom: $bottom-space;
	max-height: (1.6rem*1.875*9);

	.no-js & {
		max-height: none; }

	p:last-child {
		margin-bottom: 0; } }

.text-slide__button {
	.btn-arrow--bottom.active > span {
		&::before {
			transform: rotate(-45deg); }

		&::after {
			transform: rotate(45deg); } } }

///*=================*/
/*history info end */
///*=================*/

///*=================*/
/*media block */
///*=================*/
.media-block {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.media-block__nav {
	border-bottom: 1px solid shade($border-color, 80%);
	margin-bottom: 20px;
	@include clear;

	@include media('<mob-md') {
		display: none; } }

.media-block__tab {
	float: left;
	margin-bottom: -1px;

	a {
		display: inline-block;
		vertical-align: top;
		padding: 12px 20px;
		font-family: $font-alt;
		font-weight: 400;
		font-size: 1.2rem;
		line-height: 1.5;
		text-decoration: none;
		color: $text-color-light;
		text-transform: uppercase;
		border: 1px solid transparent;
		border-bottom-color: shade($border-color, 80%);
		background-color: $bg-color;
		transition-property: border-color, color;
		transition-duration: $animation-speed;

		@include media('<mob-md') {
			font-size: 1.0rem;
			padding: 10px 8px; }

		.no-touchevents &:hover {
			color: $text-color-alt; }

		&.active-tab {
			&, .no-touchevents &:hover {
				border-color: shade($border-color, 80%);
				border-bottom-color: transparent;
				color: $text-color;
				cursor: default; } } }

	&:first-child a {
		&, .no-touchevents &:hover {
			border-left-color: transparent; } } }

.media-block__panel {
	overflow: hidden;

	@include media('<mob-md') {
		opacity: 1 !important;
		visibility: visible !important;
		position: relative !important;
		left: auto !important;
		top: auto !important;
		margin-bottom: $bottom-space;

		&:last-child {
			margin-bottom: 0; } }

	.section__subtitle {
		display: none;

		@include media('<mob-md') {
			display: block; } } }

.media-list {
	margin-bottom: $bottom-space;
	@include grid-parent($grid-space);
	@include clear-spacing;

	&:last-child {
		margin-bottom: 0; }

	.media-block__panel & {
		margin-bottom: -$grid-space; }

	@include media('<mob-md') {
		@include grid-parent(2px);

		.media-block__panel & {
			margin-bottom: -2px; } } }

.media-list__item {
	@include grid-item(4, $grid-space);
	@include font(1.4rem, 1.3);
	letter-spacing: normal;
	text-align: center;

	@include media('<desk-lg') {
		@include grid-item(2, $grid-space); }

	@include media('<mob-md') {
		@include grid-item(2, 2px); } }

.media-list__inner {
	display: block;
	height: 100%; // для js equalHeight
	text-decoration: none;
	color: $text-color;
	padding: 10px;
	background-color: white;
	transition-property: color;
	transition-duration: $animation-speed;

	.no-touchevents &:hover {
		color: $text-color-alt; } }

.media-list__img {
	position: relative;
	overflow: hidden;
	margin-bottom: $bottom-space-sm; }

.media-list__figure {
	position: relative;
	@include ratio(1, 1);
	transition-property: transform, opacity;
	transition-duration: $animation-speed;

	img {
		position: absolute;
		left: 50%;
		top: 50%;
		max-width: 100%;
		max-height: none;
		min-width: 0;
		min-height: 0;
		width: calc(100% + 4px);
		height: calc(100% + 4px);
		transform: translate(-50%, -50%);
		text-align: left;
		object-fit: cover;
		backface-visibility: hidden;

		.no-objectfit & {
			width: auto;
			height: 100%; } } }

//.no-touchevents .media-list__inner:hover .media-list__figure
//ransform: scale(1.1)
//pacity: 0.85

.media-list__item .video-play {
	background-color: rgba(0, 0, 0, 0.1);
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	position: absolute;
	transition: background-color 0.15s ease 0s;

	img {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(50px);
		transform: translate(-50%, -50%); } }

.media-list__title {
	padding-bottom: 5px; }

.popup-gallery {
	display: none; }

///*=================*/
/*media block end */
///*=================*/

///*=================*/
/*survey */
///*=================*/
.survey {
	padding: $grid-inside-vertical-space $grid-inside-horizontal-space;
	background-color: $text-color-alt;
	color: white; }

.survey__header {
	margin-bottom: 20px;
	@include font(1.4rem, 1.5);
	color: rgba(255, 255, 255, 0.51);
	@include clear;

	& > span {
		display: block;
		overflow: hidden; } }

.survey__all {
	float: right;
	margin-left: 15px;
	color: inherit; }

.survey__title {
	text-align: center;
	margin-bottom: 30px;
	font-family: $font-alt;
	@include font(1.6rem, 1.2);
	color: white; }

.survey__answers {
	&, .#{$user-content} & {
		margin-bottom: 30px;

		&:last-child {
			margin-bottom: 0; } }

	li {
		margin: 2rem 0; } }

.survey__answers {
	line-height: 1;
	min-height: 188px;

	input[type="checkbox"] + label,
	input[type="radio"] + label,
	.check-label,
	.radio-label {
		@include font(14px, 18px);
		color: white; }

	input[type="checkbox"],
	input[type="radio"] {
		& + label,
		& + span i {
			&::before {
				@include size(20px);
				border-width: 0;
				background-color: white; } }

		& + label span,
		& + span {
			padding: 1px 0 1px 35px; } }

	input[type="checkbox"] {

		& + label span,
		& + span {
			&::before,
			&::after {
				@include size(0, 4px);
				top: 10px; }

			&::after {
				left: 22px; } }

		// checkbox checked
		&:checked {

			& + label span,
			& + span {
				&::before, &::after {
					background-color: $mark-color; }

				&::before {
					width: 9px;
					margin: -5px 0 0 0; }

				&::after {
					width: 12px;
					margin: 0 0 0 -8px; } } }

		// checkbox disabled
		&:disabled {

			& + label span,
			& + span {
				&::before, &::after {
					background: lighten($text-color, 30%); } } } }

	// radio
	input[type="radio"] {

		& + label span,
		& + span i {

			&::after {
				@include size(12px);
				top: 4px;
				left: 4px;
				background: $mark-color; } }

		// radio disabled
		&:disabled {
			& + label,
			& + span {
				color: white; }

			& + label span,
			& + span i {
				&::after {
					background: $text-color; } } } } }

.survey__footer {
	display: table;
	width: 100%;
	height: 40px;

	a {
		display: table-cell;
		vertical-align: middle;
		text-align: center;
		color: rgba(255, 255, 255, 0.51); }

	[class*="btn-default-"] {
		padding: 8px; } }


///*=================*/
/*survey end */
///*=================*/

///*=================*/
/*faq */
///*=================*/
/*expander */
.expander {
	margin-bottom: $bottom-space-lg;

	@include media('<mob-lg') {
		@include margin-horizontal(-#{$article-layout-space-horizontal--mob-lg}); }

	&:last-child {
		margin-bottom: 0; } }

.expander-item {
	border: 1px solid $border-color;
	margin-bottom: $bottom-space;
	padding: 0 $grid-inside-horizontal-space;

	@include media('<tab-lg') {
		@include padding-horizontal($article-layout-space-horizontal--tab-lg); }

	@include media('<mob-lg') {
		@include padding-horizontal($article-layout-space-horizontal--mob-lg);
		margin-bottom: 0;
		border-width: 0 0 1px;

		&:first-child {
			border-top-width: 1px; } }

	&:last-child {
		margin-bottom: 0; }

	p, ul, ol {
		max-width: none; }

	.#{$user-content} & ul > li::before {
		top: calc(1em * #{1.25} / 2); } }

.expander-header {
	position: relative;
	padding: $grid-inside-vertical-space 0;
	transition-property: border-color;
	transition-duration: $animation-speed;
	@include clear;

	@include media('<tab-lg') {
		@include padding-vertical($article-layout-space-vertical--tab-lg); }

	@include media('<mob-lg') {
		@include padding-vertical($article-layout-space-vertical--mob-lg); } }

.expander-header__meta {
	@include font(1.2rem, 1.7);
	color: $text-color-light;
	margin-bottom: $bottom-space;

	@include media('<desk-lg') {
		display: block;
		float: none;
		width: auto;
		margin-bottom: $bottom-space-sm; }

	@include media('<mob-lg') {
		@include font($font-size, $line-height); }

	time {
		@include font-normal;

		@include media('<mob-lg') {
			display: inline-block;
			margin-right: 20px; } }

	.name {
		display: inline-block;
		margin-right: 8px;

		& ~ time {
			&::before {
				@include pseudo($content: '/', $display: inline-block, $pos: relative);
				margin-right: 8px; } } } }

.expander-header__text {
	font-family: $font-alt;
	@include font(1.6rem, 1.25);
	font-weight: normal;
	color: $text-color;
	transition-property: color;
	transition-duration: $animation-speed;
	margin-bottom: $bottom-space; }

.expander-angle {
	@include display-inline-block-top;
	position: relative;
	cursor: pointer;
	line-height: 18px;
	@include antialiased;

	.no-js & {
		display: none; }

	& > span {
		@include display-inline-block-top;
		position: relative;
		font-family: $font-alt;
		text-transform: uppercase;
		@include font(0, 0);
		font-weight: normal;
		white-space: nowrap;

		&:before, &:after {
			@include display-inline-block-top;
			@include font(12px, 18px);
			position: relative;
			border-bottom: 1px dotted $text-color;
			transition-property: opacity, transform, visibility;
			transition-duration: $animation-speed; }

		&:before {
			content: 'Показать ответ';
			transform: translate(0, 0);

			.expander-is-open & {
				opacity: 0;
				visibility: hidden;
				transform: translate(0, -1em); } }

		&:after {
			content: 'Закрыть ответ';
			position: absolute;
			@include top-left(0);
			@include size(100%);
			opacity: 0;
			visibility: hidden;
			width: auto;
			transform: translate(0, 1em);

			.expander-is-open & {
				opacity: 1;
				visibility: visible;
				transform: translate(0, 0); } }

		.no-touchevents &:hover {
			color: $text-color-alt;

			&:before, &:after {
				border-bottom-color: $text-color-alt; } } }

	& > i {
		display: none; } }

.expander-panel {
	position: relative;
	overflow: hidden;
	border-top: 1px solid $border-color;
	display: none;
	@include font($font-size-small, $line-height-small);

	.no-js & {
		display: block; } }

.expander-panel__holder {
	padding: $grid-inside-vertical-space 0;

	@include media('<tab-lg') {
		@include padding-vertical($article-layout-space-vertical--tab-lg); }

	@include media('<mob-lg') {
		@include padding-vertical($article-layout-space-vertical--mob-lg); } }

/*expander end */

/*faq form */
.faq-form-wrap {
	margin-bottom: $bottom-space-lg;

	&:last-child {
		margin-bottom: 0; } }

/*faq form end */
