@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*base */
html {
	font-size: 62.5%; }

body {
	@include font-normal;
	@include font($font-size, $line-height);
	height: 100%;

	color: $text-color;
	background: $bg-color url(../../img/empty.png) repeat fixed;
	min-width: 320px;

	@include media('<desk-hd') {
		@include font($font-size, 1.7); } }

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	@include font-title;
	color: $title-color;
	margin: 0 0 $bottom-space;
	text-align: left;

	@include media('<desk-hd') {
		margin-bottom: 1.5rem; }

	@include media('<mob-lg') {
		margin-bottom: 1rem; } }

h1, .popup-title h2 {
	@include font($font-size-h1, $line-height-h1);

	@include media('<desk-hd') {
		@include font($font-size-h1--sm, $line-height-h1--sm); } }

h2, .popup-title h3 {
	@include font($font-size-h2, $line-height-h2);

	@include media('<desk-hd') {
		@include font($font-size-h2--sm, $line-height-h2--sm); } }

h3 {
	@include font($font-size-h3, $line-height-h3);

	@include media('<desk-hd') {
		@include font($font-size-h3--sm, $line-height-h3--sm); } }

h4 {
	@include font($font-size-h4, $line-height-h4);

	@include media('<desk-hd') {
		@include font($font-size-h4--sm, $line-height-h4--sm); } }

h5, h6 {
	font-weight: 400;
	@include font($font-size, 1.5); }

.text-title {
	@include font-bold;
	@include font(1.8rem, $line-height);
	margin-bottom: $bottom-space-sm;

	.content & + ul,
	.content & + ol,
	.content & + table {
		margin-top: $bottom-space-sm; } }

p {
	padding: 0;
	margin: 0 0 $bottom-space;

	@include media('<desk-hd') {
		margin-bottom: $bottom-space-sm; } }
hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space;

	@include media('<desk-hd') {
		margin-bottom: $bottom-space-sm; } }

a {
	color: $link-color;
	//outline: none
	text-decoration: underline;
	cursor: default;

	&:link {
		cursor: pointer; }

	.no-touchevents &:hover {
		text-decoration: none; }

	&[href^="tel"],
	.no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: 200px;
		//width: 200px
		height: auto; } }

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin: 0 0 $bottom-space;

	@include media('<desk-hd') {
		margin-bottom: $bottom-space-sm; }

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }

.table-auto-wrap {
	position: relative;

	.topscroll {
		width: 100%;
		height: 17px;
		overflow-y: auto;

		.touchevents & {
			display: none; } }

	.topscroll-hand {
		height: 1px; } }

table {
	border-collapse: collapse;
	border-spacing: 0;
	width: 100%;

	td, th {
		@include font($font-size, $line-height);
		color: $color-table-text;
		padding: 12px 16px;
		box-sizing: border-box;
		border-color: $border-color;
		text-align: left;

		@include media('<mob-lg') {
			padding: 10px 18px; } }

	th {
		font-size: 1.6rem; } }

b, strong, th {
	@include font-bold; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

.entry-text {
	font-size: 1.8rem;
	line-height: 1.3;
	font-weight: 700;
	margin-bottom: $bottom-space;

	@include media('<desk-hd') {
		margin-bottom: $bottom-space-sm; }

	.#{$user-content} & p, ul, ol {
		max-width: none; }

	p:last-child {
		margin-bottom: 0; } }

.mark {
	color: $mark-color;
	font-style: inherit;
	font-weight: inherit; }

.dark-text {
	color: $text-color; }

.dark-text-light {
	color: $text-color-light; }

.text-color-alt {
	color: $text-color-alt; }

.align-center {
	text-align: center; }

.nobr {
	white-space: nowrap; }

.hide {
	display: none; }

.float-left {
	float: left; }

.float-right {
	float: right; }

@include media('<desk-hd') {
	.hide-desk-lg {
		display: none !important; } }

@include media('<desk-lg') {
	.hide-desk-md {
		display: none !important; } }

@include media('<desk-md') {
	.hide-desk-sm {
		display: none !important; } }

@include media('>=tab-lg') {
	.show-960 {
		display: none!important; } }

@include media('<tab-lg') {
	.hide-960 {
		display: none!important; } }

.#{$user-content} {
	@each $tags in h2, h3, h4, h5 {
		p + #{$tags},
		ul + #{$tags},
		ol + #{$tags} {
			margin-top: 4rem;

			@include media('<desk-hd') {
				margin-top: 2rem; }

			@include media('<mob-lg') {
				margin-top: 1rem; } } } }

/*base end */

/*list */
.#{$user-content} {
	ul, ol {
		margin: $bottom-space 0;
		padding: 0;
		list-style: none;

		@include media('<desk-hd') {
			@include margin-vertical($bottom-space-sm); }

		ul, ol {
			@include margin-vertical(round($bottom-space/2));

			@include media('<desk-hd') {
				@include margin-vertical(round($bottom-space-sm/2)); } } }

	ul li, ol li {
		padding-left: 30px;
		margin: #{$bottom-space-sm} 0;
		position: relative;

		@include media('<desk-hd') {
			@include margin-vertical(round($bottom-space-sm/2)); } }

	ul > li::before {
		content: '';
		//position: absolute
		display: block;
		float: left;
		left: 0;
		//top: calc(1em * #{$line-height} / 2)
		margin: 0.5em 0 -0.5em -30px;
		height: 0;
		width: 0;
		border: solid transparent;
		border-width: 5px 0 5px 9px;
		border-left-color: $mark-color;
		box-sizing: border-box; }

	ol {
		counter-reset: section;

		& > li {
			counter-increment: section;
			//height: 1%
			//overflow: hidden
			display: inline-block;
			vertical-align: top;
			width: 100%;

			&::before {
				position: relative;
				display: inline-block;
				text-align: left;
				@include font-title;
				color: $mark-color;
				content: counter(section);
				padding: 0 4px 0 0;
				min-width: 30px;
				margin-left: -30px;
				box-sizing: border-box; } } }

	//list reset
	.list-reset {
		list-style: none;
		margin: 0;

		li, li:first-child {
			padding: 0;

			&::before, &:first-child::before {
				content: none; } } } }
/*list end */

/*table */
.#{$user-content} {
	table {
		margin-bottom: $bottom-space;

		@include media('<desk-hd') {
			margin-bottom: $bottom-space-sm; } }

	.table-auto table {
		margin-bottom: 0; }

	.tbl {
		border: none;

		td, th {
			color: $color-table-text;
			background-color: $color-table-bg;
			border: 0 solid $border-color;
			font-size: 1.4rem;
			line-height: 1.2;

			&:first-child,
			&:first-child {
				border-left-width: 1px; }

			&:last-child,
			&:last-child {
				border-right-width: 1px; } }

		td {
			border-top-width: 1px; }

		tr:last-child td {
			border-bottom-width: 1px; }

		tbody tr:nth-child(even) td {
			background-color: #fafafa; }

		th, thead td {
			@include font-bold;
			font-size: 1.6rem;
			text-align: left; }

		th, thead td, tfoot td {
			background-color: $color-table-bg-alt;
			color: $color-table-text-alt;
			border-color: $color-table-bg-alt;
			border-top-width: 0; } }

	//.tbl:not([border="1"])
	//body tr:first-child td
 }	//border-top-width: 1px

/*table end */

/*blockquote */
.#{$user-content} {
	.blockquote {
		margin: $bottom-space-md 0;

		@include media('<desk-hd') {
			margin-bottom: $bottom-space-sm; } }

	blockquote {
		display: block;
		margin: 0;
		padding: 0;

		q {
			display: block;
			position: relative;
			margin: 0;
			padding: 0;
			font-family: $font-alt;
			@include font(2rem, 1.5);
			color: $text-color;
			z-index: 5;

			@include media('<mob-lg') {
				@include font(1.6rem, 1.7); }

			&::before, &::after {
				@include pseudo($pos: relative);
				background: url(../../img/quote-left.svg) no-repeat center center;
				background-size: contain;
				@include size(39px);
				z-index: -1;
				font-size: 0;
				line-height: 0;
				letter-spacing: -4px; }

			&::before {
				content: '«';
				margin: 0 0 20px 30%;

				@include media('<mob-lg') {
					@include margin($b: 10px, $l: 0 ); } }

			&::after {
				content: ' »';
				margin: 20px 30% 0 auto;
				transform: rotate(180deg);

				@include media('<mob-lg') {
					content: none;
					display: none; } } }

		cite {
			text-align: right;
			display: block;
			margin-left: auto;
			@include font-title;
			font-size: $font-size;
			line-height: $line-height;
			color: $text-color; } } }
/*blockquote end */

/*content img */
.imgc {
	text-align: center;
	padding-bottom: $bottom-space-lg;
	clear: both;

	@include media('<desk-hd') {
		padding-bottom: $bottom-space; }

	@include media('<mob-lg') {
		padding-bottom: $bottom-space-sm; } }

.imgl {
	text-align: left;
	float: left;
	margin: $bottom-space*0.3 $bottom-space-lg $bottom-space-md 0;
	max-width: 50%;

	@include media('<desk-hd') {
		margin-bottom: $bottom-space; }

	@include media('<desk-lg') {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 100%; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space-sm; } }

.imgr {
	text-align: right;
	float: right;
	margin: $bottom-space*0.3 0 $bottom-space-lg $bottom-space-md;
	max-width: 50%;

	@include media('<desk-hd') {
		margin-bottom: $bottom-space; }

	@include media('<desk-lg') {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: none; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space-sm; } }

/*content img end */

/*view print */
.view-print {
	display: inline-block;
	vertical-align: top;
	padding: 4px 18px;
	background: white;
	font-size: 16px;
	line-height: 30px;
	color: $text-color-alt;
	border: 1px solid $text-color-alt;
	text-decoration: none;
	margin-bottom: $bottom-space;
	transition-property: background-color, color;
	transition-duration: $animation-speed;

	@include media('<tab-lg') {
		display: none; }

	&:hover {
		background-color: $text-color-alt;
		color: white; }

	&:active {
		transform: scale(0.98); }

	svg {
		width: 21px;
		height: 21px;
		vertical-align: middle;
		margin-right: 10px; }

	path {
		fill: $text-color-alt;
		transition-property: fill;
		transition-duration: $animation-speed; }

	.no-touchevents &:hover path {
		fill: white; }

	span {
		display: inline-block;
		vertical-align: middle; }

	@include media('<desk-lg') {
		& svg {
			margin-right: 0; }

		& span {
			display: none; } }

	@include media('<tab-lg') {
		font-size: 14px; } }

/*view print end*/

/*video */
.video {
	margin-bottom: $bottom-space; }

.video-holder {
	position: relative;
	padding-bottom: 56%;
	height: 0; }

.video-holder iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%; }

/*video end */

.text-warning {
	display: block;
	position: relative;
	@include font($font-size-small, $line-height-small);
	padding-left: 48px;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	&::before {
		@include pseudo($content: "!");
		@include font-bold;
		@include size(28px);
		@include font(24px, 28px);
		color: white;
		text-align: center;
		left: 0;
		top: 50%;
		transform: translate(0, -50%);
		background-color: $mark-color;
		border-radius: 50%; } }

.rss {
	$rss-bg-color: #f8991d;

	&, .section__title & {
		display: inline-block;
		background-color: $rss-bg-color;
		text-decoration: none;
		color: white;
		fill: white;
		padding: 4px 10px;
		font-family: $font-alt;
		font-weight: normal;
		@include font(16px, 20px);
		transition-property: background-color;
		transition-duration: $animation-speed;

		svg {
			@include display-inline-block-top;
			@include size(16px);
			margin-top: 2px;
			margin-right: 4px; }

		path {
			fill: inherit; }

		span {
			@include display-inline-block-top;
			border: none;
			text-transform: uppercase; } }

	.section__title & {
		position: absolute;
		right: 0;
		top: 0; } }

.typography-page h3 {
	position: relative;

	a {
		color: inherit;
		text-decoration: none; } }

.hash-anchor {
	width: 100%;
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	margin-top: -100px;

	&::before {
		@include pseudo();
		right: 100%;
		top: 4px;
		margin-right: 10px;
		margin-top: 100px;
		@include size(15px, 20px);
		background-repeat: no-repeat;
		background-image: url('data:image/png;base64,');
		background-size: contain;
		backface-visibility: hidden;
		opacity: 0;
		transition: opacity $animation-speed; } }

.no-touchevents h3:hover {
	& > .hash-anchor::before {
		opacity: 0.5; } }

.color--orange {
	color: $mark-color-alt; }

.img-zoom {
	//cursor: url(../../img/cursor-zoom.png), zoom-in
	cursor: pointer; }

.zoom-img-popup .lg-toolbar {
	background: rgba(0, 0, 0, 0.20); }

.fluid-width-video-wrapper {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.depict-mchs {
	@include display-inline-block-top;
	background: url(../../img/logo.png) no-repeat;
	background-size: contain;
	@include size(42px);
	backface-visibility: hidden; }

/* page-nav */
.page-nav {
	@include clear;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	.page-prev, .page-next {
		position: relative;
		@include display-inline-block-top;
		@include margin-horizontal($grid-space);
		@include font-title;
		@include font(20px, round-float(28/20, 2));
		text-decoration: none;
		color: $base-color;

		@include media('<mob-lg') {
			font-size: 14px; }

		&::before,
		&::after {
			content: '';
			position: absolute;
			top: 50%;
			height: 2px;
			width: 10px;
			border-radius: 2px;
			background: $base-color;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		&::before {
			margin: -2px 0 0 0;
			transform: rotate(-45deg); }

		&::after {
			margin: 0;
			transform: rotate(45deg); }

		span {
			border-bottom: 1px dotted $base-color; }

		// hover
		&:hover {
			span {
				border-bottom-color: transparent; } } }

	.page-prev {
		float: left;
		padding-left: 18px;
		margin-left: 0;

		&::before, &::after {
			left: 0; }

		&::before {
			margin: -2px 0 0; }

		&::after {
			margin: 4px 0 0; } }

	.page-next {
		float: right;
		padding-right: 18px;
		margin-right: 0;

		&::before, &::after {
			right: 0; }

		&::before {
			margin: 4px 0 0; }

		&::after {
			margin: -2px 0 0; } } }

/* page-nav end */

/* phenomena map */
.events-map {
	padding: 0;

	@include media('<mob-lg') {
		@include margin-horizontal(-$grid-layout-space--mob-lg); } }

.events-map__content {
	width: auto;
	margin-bottom: 0; }

.events-map__iframe__wrap {
	position: relative;

	&::before {
		content: "";
		display: block;
		padding-top: 98%;

		@include media('<1800px') {
			padding-top: 125%; }

		@include media('<1600px') {
			padding-top: 126%; }

		@include media('<1440px') {
			padding-top: 130%; }

		@include media('<1366px') {
			padding-top: 134%; }

		@include media('<1280px') {
			padding-top: 130%; }

		@include media('<1024px') {
			padding-top: 132%; }

		@include media('<960px') {
			padding-top: 130%; }

		@include media('<768px') {
			padding-top: 135%; }

		@include media('<640px') {
			padding-top: 150%; }

		@include media('<480px') {
			padding-top: 167%; }

		@include media('<360px') {
			padding-top: 188%; } } }

.events-map__iframe {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	@include linear-gradient(to bottom, #fbfcfe 0, #e6f0f6 50%, #dbe9f2 100%); }

/* phenomena map end */

/* preloader-sm */
.preload-sm {
	width: 48px;
	height: 48px;
	background-image: url(../../img/preloader.svg);
	background-repeat: no-repeat;
	background-size: contain;
	z-index: 9999;
	pointer-events: none;

	.news-grid__slider & {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); }

	.slider-inited & {
		display: none; } }

/* preloader-sm end */

/* img-fit-js */
.img-fit-js,
.img-fit-js--contain {
	display: block;
	width: 100%;
	height: 100%;
	max-width: none; }

.img-fit-js {
	@include object-fit(cover); }

.img-fit-js--contain {
	@include object-fit(contain); }

/* img-fit-js end */
.no-js-warning {
	border: 2px solid #f00;
	color: #f00;
	background: #fff;
	padding: 5px 20px;
	font-size: 18px; }

/*go to site mchs */
.go-to-mchs, .go-to-mchs--mob, .go-to-mchs--dep {
	float: left;
	position: relative;
	width: $go-to-mchs-width;
	text-align: center;
	font-size: 1.4rem;
	line-height: 1.4;
	text-decoration: none;
	color: white;
	height: $header-top-height;
	@include linear-gradient(to right, #006ab8 0%, #00487c 100%);
	@include padding-horizontal(52px, 12px);

	@include media('<desk-lg') {
		height: $header-top-height--md; }

	@include media('<tab-lg') {
		display: none; }

	&::before {
		@include vertical-align(); }

	&::after {
		@include pseudo();
		left: 10px;
		top: 50%;
		@include size(40px);
		transform: translate(0, -50%);
		background: url(../../img/logo.png) no-repeat center;
		background-size: contain; }

	span {
		display: inline-block;
		vertical-align: middle;
		width: 98%; } }

.go-to-mchs--dep {
	height: auto;
	float: none;
	width: 100%;
	clear: both;
	padding: 3px 10px 3px 60px;
	text-align: left;
	display: none;

	@include media('<tab-lg') {
		min-height: $header-top-height--md;
		display: block;

		&::before {
			height: $header-top-height--md - 6; } } }

.go-to-mchs--mob {
	float: none;
	width: 100%;
	clear: both;
	padding-left: 60px;
	text-align: left;
	display: none;

	@include media('<mob-md') {
		display: block; } }

/*go to site mchs end */
