@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*BUTTONS*/
[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	text-decoration: none;
	cursor: pointer; }

[class|="btn"], [class*=" btn-"] {
	& input[type="submit"], & input[type="button"], & input[type="reset"] {
		background: url("../../img/empty.png") repeat;
		display: block;
		@include cover();
		padding: 0;
		z-index: 99;
		border: 0;
		opacity: 0; } }

/* depicts */
.depict-tech,
.depict-nature,
.depict-fire,
.depict-man-save,
.depict-man-dead {
	@include display-inline-block-top;
	@include size(30px);
	background-repeat: no-repeat;
	background-position: center; }

.depict-fire {
	background-image: url(../../img/depict-fire.svg); }

.depict-man-save {
	background-image: url(../../img/depict-man-save.svg); }

.depict-nature {
	background-image: url(../../img/depict-nature.svg); }

.depict-man-dead {
	background-image: url(../../img/depict-man-dead.svg); }

.depict-tech {
	background-image: url(../../img/depict-tech.svg); }

/* depicts end */

/*btn-default*/
.btn-default {
	position: relative;
	font: 400 1.6rem/24px $font-alt;
	color: $btn-text-color;
	padding: 11px 25px;
	border: 2px solid transparent;
	background-color: $btn-bg-color;
	box-shadow: none;
	transition-property: background-color, color, border-color;
	transition-duration: $animation-speed;
	@include btn-hover;
	@include btn-active;

	@include media('<desk-hd') {
		font-size: 1.4rem;
		line-height: 18px; }

	@include media('<mob-lg') {
		padding: 6px 20px; }

	&:disabled {
		cursor: default;
		background-color: $btn-disabled-color;

		.no-touchevents & {
			background-color: $btn-disabled-color; } } }

/*btn-default end*/

/*btn-default-alt*/
.btn-default-alt {
	position: relative;
	font: 400 1.6rem/24px $font-alt;
	padding: 11px 25px;
	background-color: white;
	box-shadow: none;
	color: $btn-bg-color;
	border: 2px solid $btn-bg-color;
	transition-property: background-color, color, border-color;
	transition-duration: $animation-speed;

	@include media('<desk-hd') {
		font-size: 1.4rem;
		line-height: 18px; }

	@include media('<mob-lg') {
		padding: 6px 20px; }

	.no-touchevents &:hover {
		background: $btn-bg-color;
		color: $btn-text-color; }

	&:disabled {
		cursor: default;
		background-color: white;
		border-color: $btn-disabled-color;
		color: $btn-disabled-color;

		.no-touchevents & {
			background-color: white;

			&:hover {
				color: $btn-disabled-color; } } } }

/*btn-default-alt end*/

/*btn-default-light */
.btn-default-light {
	position: relative;
	font: 400 1.6rem/24px $font-alt;
	padding: 13px;
	background-color: white;
	box-shadow: none;
	border: none;
	color: $text-color;
	transition-property: opacity;
	transition-duration: $animation-speed;

	@include media('<desk-hd') {
		font-size: 1.4rem;
		line-height: 18px; }

	@include media('<mob-lg') {
		padding: 6px 20px; }

	.no-touchevents &:hover {
		opacity: 0.5; }

	&:disabled {
		cursor: default;
		background-color: white;
		border-color: $btn-disabled-color;
		color: $btn-disabled-color;

		.no-touchevents & {
			background-color: white;

			&:hover {
				color: $btn-disabled-color; } } } }

/*btn-default-light end */

/*btn-arrow*/
.btn-arrow {
	position: relative;
	font: 400 1.6rem/24px $font-alt;
	color: $btn-text-color;
	background-color: $btn-bg-color;
	box-shadow: none;
	border: 1px solid transparent;
	padding: 7px 20px;
	transition-property: background-color, color, border-color;
	transition-duration: $animation-speed;

	@include media('<desk-hd') {
		font-size: 1.4rem;
		line-height: 18px; }

	.no-touchevents &:hover {
		background-color: $btn-bg-color-hover; }

	& > span {
		display: inline-block;
		vertical-align: top;
		position: relative;

		&::before,
		&::after {
			content: '';
			position: absolute;
			top: 50%;
			height: 2px;
			width: 10px;
			border-radius: 2px;
			background: #fff;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		&::before {
			margin: -4px 0 0 0;
			transform: rotate(-45deg); }

		&::after {
			margin: 2px 0 0 0;
			transform: rotate(45deg); } }

	&--left > span {
		padding-left: 18px;

		&::before, &::after {
			left: 0; }

		&::before {
			margin: -4px 0 0 0; }

		&::after {
			margin: 2px 0 0 0; } }

	&--right > span {
		padding-right: 18px;

		&::before, &::after {
			right: 0; }

		&::before {
			margin: 2px 0 0 0; }

		&::after {
			margin: -4px 0 0 0; } }

	&--bottom > span {
		padding-right: 22px;

		&::before, &::after {
			margin: 0; }

		&::before {
			right: 6px;
			transform: rotate(45deg); }

		&::after {
			right: 0;
			transform: rotate(-45deg); } }

	&--top > span {
		padding-right: 22px;

		&::before, &::after {
			margin: 0; }

		&::before {
			right: 6px;
			transform: rotate(-45deg); }

		&::after {
			right: 0;
			transform: rotate(45deg); } } }

/*btn-arrow*/

/* btn with icon */
.btn-with-icon {
	& > i {
		vertical-align: middle;
		margin-right: 20px; } }

.to-mchs a {
	display: block;
	text-align: left; }

/* btn with icon end */

/*more button*/
.more {
	text-align: center;
	padding-bottom: 7px;

	.btn-more {
		display: inline-block;
		position: relative;
		text-decoration: none;
		font: 400 #{$font-size}/#{$line-height} $font-alt;
		color: $text-color-alt;
		padding-bottom: 15px;
		@include antialiased;
		@include btn-active;

		&::before {
			@include pseudo();
			top: -2px;
			bottom: -7px;
			left: -12px;
			right: -12px;
			background-color: $border-color;
			opacity: 0;
			transform: scale(0.85);
			transition-property: opacity, transform;
			transition-duration: $animation-speed;
			z-index: -1; }

		.no-touchevents &:hover::before {
			opacity: 1;
			transform: scale(1); }

		i {
			@include size(17px, 16px);
			position: absolute;
			left: 50%;
			bottom: 0;
			font-size: 1px;
			pointer-events: none;
			backface-visibility: hidden;
			transform: translate(-50%, 0);
			z-index: 2; }

		i::before, i::after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			@include size(12em, 2em);
			border-radius: 2em;
			background: $text-color-alt;
			backface-visibility: hidden; }

		i::before {
			margin: -1em 0 0 -10em;
			transform: rotate(45deg); }

		i::after {
			margin: -1em 0 0 -3em;
			transform: rotate(-45deg); } } }

/*more button end*/

/*button map site*/
.btn-map-site {
	//<a href="#" class="btn-map-site"><i></i><span>Карта сайта</span></a>
	$btn-bg: $btn-bg-color;
	$btn-color: #fff;
	$btn-min-height: 40px;
	$btn-space: 12px;
	$btn-lines-width: 22px;
	$btn-lines-height: 3px;
	$btn-lines-space: 5px;
	$btn-lines-color: $btn-color;

	@mixin line-style() {
		height: $btn-lines-height;
		width: $btn-lines-width;
		background: $btn-lines-color; }

	@mixin font() {
		font: 300 14px/1.4 $font-norm;
		letter-spacing: normal; }

	@mixin transition() {
		transition-property: transform, box-shadow, background-color;
		transition-duration: $animation-speed;
		transition-timing-function: ease-in-out; }

	display: inline-block;
	text-decoration: none;
	color: #fff;
	background-color: $btn-bg;
	font-size: 0;
	line-height: 0;
	letter-spacing: -4px;
	white-space: nowrap;
	transition-property: background-color, color;
	transition-duration: $animation-speed;
	@include antialiased;
	@include btn-active;

	&::before {
		content: '';
		display: inline-block;
		height: $btn-min-height;
		min-height: 100%;
		vertical-align: middle; }

	> span {
		@include font;
		display: inline-block;
		vertical-align: middle;
		position: relative;
		max-width: 98%;
		white-space: nowrap;
		margin: 0 $btn-space; }

	> i {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		margin-left: $btn-space;
		backface-visibility: hidden;
		@include line-style;

		&::before, &::after {
			@include pseudo();
			left: 0;
			backface-visibility: hidden;
			@include line-style; }

		//first line menu
		&::before {
			top: -#{$btn-lines-height + $btn-lines-space}; }

		//third line menu
		&::after {
			top: #{$btn-lines-height + $btn-lines-space}; } } }

/*button map site end*/
/*BUTTONS end*/
