@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*download files items*/
.files {
	@include grid-parent($grid-space);
	@include clear-spacing;
	margin-bottom: $bottom-space;

	.typography-page & {
		position: relative;

		&::before {
			@include pseudo();
			@include cover($position: null);
			background-color: white; } } }


.files__item {
	@include font($font-size-small, $line-height-small);
	letter-spacing: normal;

	.files & {
		@include grid-item(2, $grid-space);

		@include media('<desk-lg') {
			@include grid-item(1, $grid-space); } } }

.files__inner {
	display: block;
	position: relative;
	padding: #{$grid-inside-vertical-space} #{$grid-inside-horizontal-space};
	text-decoration: none;
	color: $text-color;
	background: #fff;
	width: 100%;
	box-shadow: inset 0 0 0 2px $border-color;
	transition-property: box-shadow;
	transition-duration: $animation-speed-mouseleave;
	transition-timing-function: ease-in-out;
	@include clear;

	@include media('<mob-lg') {
		padding: #{round($grid-inside-vertical-space*0.5)} #{round($grid-inside-horizontal-space*0.5)}; }

	$files-border-colors: (pdf: #a00, word: #2b5797, excel: #02723b, audio: #ff9a09, link: #259deb, rtf: #0868f8);

	.files & {
		height: 100%; }

	.no-touchevents &:hover {
		transition-duration: $animation-speed; }

	& {
		@each $files, $this-file-border-color in $files-border-colors {
			.no-touchevents &.files_#{$files}:hover {
				box-shadow: inset 0 0 0 2px $this-file-border-color; } } } }

.files__depict {
	float: left;
	@include size(38px, 50px);
	margin-right: #{$grid-inside-horizontal-space};

	@include media('<mob-lg') {
		@include size(26px, 34px);
		@include margin($r: #{round($grid-inside-horizontal-space*0.5)}, $b: 6px); }

	img {
		max-width: 100%;
		max-height: 100%; } }

.files__description {
	overflow: hidden;
	position: relative;
	padding-bottom: 30px;
	height: 100%;

	@include media('<mob-lg') {
		overflow: visible;
		padding-bottom: 0; } }

.files__title {
	margin-bottom: 10px; }

.files__meta {
	$space: 30px;
	position: absolute;
	bottom: 0;
	margin-left: -$space;

	@include media('<mob-lg') {
		position: relative;
		bottom: auto;
		margin-left: 0; }

	& > span {
		display: inline-block;
		vertical-align: top;
		padding-left: 26px;
		margin-left: $space;
		background-size: 15px;
		color: $text-color-light;

		@include media('<mob-lg') {
			margin-left: 0;
			margin-right: 20px; } } }

.files__size {
	background: url(../../img/save.svg) no-repeat left center; }

.files__date {
	background-size: 15px;
	background: url(../../img/date.svg) no-repeat left center; }

// title
.files__item--title {

	&, .files & {
		width: 100%; }

	.files__inner {
		border: none;
		padding: 0;
		box-shadow: none !important;
		cursor: default; }

	.files__depict {
		display: none; }

	.files__description {
		padding: 0; }

	.files__meta {
		display: none; }

	.files__title {
		@include font-alt-norm;
		@include font($font-size-h3, $line-height-h3);
		margin-bottom: 0;
		padding: 10px 15px; } }

/*files item end*/
/*download files items end*/
